// 验证文件是否属于需要的类型
function verificationFileType (file, typeExt) {
  // let typeExt='.jpg|.jpeg|.gif|.bmp|.png|.swf|'
  let res = true
  let fileExtName = file.name.substring(file.name.lastIndexOf('.')).toLowerCase() // （把路径中的所有字母全部转换为小写)

  if (fileExtName.indexOf(typeExt + '|') === -1) {
    alert('非法图片格式')
    res = false
  }
  return res
}

// 验证文件大小 （正确）小于最大值返会1 否则为0 单位k
function verificationFileSize (file, maxSize) {
  // maxSize = 1024
  let res = '1'
  let fileSize = 0
  let filePath = file.value
  if (filePath) {
    fileSize = file.files[0].size
    let size = fileSize / 1024
    if (size > maxSize) {
      alert('The file max size ' + maxSize + 'K')
      res = '0'
    } else if (size <= 0) {
      alert('is not file')
      res = '0'
    }
  }
  return res
}

// 异步图片尺寸验证 单位px
async function verificationPicDimension (picfile, maxWidth, maxHeight) {
  let filePath = picfile.value
  if (filePath) {
    // 读取图片数据
    let filePic = picfile.files[0]
    let reader = new FileReader()
    reader.onload = function (e) {
      let data = e.target.result
      // 加载图片获取图片真实宽度和高度
      let image = new Image()
      image.onload = function () {
        let width = image.width
        let height = image.height
        if (width !== maxWidth | height !== maxHeight) {
          alert('video image dimension ' + maxWidth.toString() + ' x ' + maxHeight.toString())
          filePath.value = ''
        }
      }
      image.src = data
    }
    reader.readAsDataURL(filePic)
  }
}

/**
 * 暂停,睡眠功能
 * 调胳膊async function test() { await sleep(1000 * 2) }
 * */
function sleep (ms) {
  return new Promise(resolve => setTimeout(resolve, ms))
}

/**
 * 返回指定范围随机数
 * */
function randomNum (start, end) {
  return parseInt(Math.random() * (end - start) + start)
}
/**
 * 从指定范围产生指定数量的随机整数数组，包含下限值，包括上限值
 * @param {Number} lower 下限
 * @param {Number} upper 上限
 * @param {Number} resNum 需要返回的数量
 * @return {Number} 返回在下限到上限之间的指定数量的随机整数（数组）
 */
function randomNumArray (lower, upper, resNum) {
  let resArray = []
  if (upper <= 0) {
    return resArray
  }
  for (let i = 0; i < resNum; i++) {
    let randomNum = Math.floor(Math.random() * (upper - lower + 1)) + lower

    // 不重复
    if (resArray.length > 0 && resArray.indexOf(randomNum) > -1) {
      i--
    } else {
      resArray.push(randomNum)
    }
  }
  return resArray
}

export default {checkExpress,
  sleep,
  randomNum,
  randomNumArray,
  clearArray,
  subStringByte,
  subStringByteDesc,
  getStringByteLength,
  verificationFileType,
  verificationFileSize,
  getFormatDateByTimestamp,
  getCurrentTime,
  getTimeOf,
  verificationPicDimension,
  findVideoInList,
  findMyUploadFileList,
  findMyUploadFileList2}



