<template>
  <v-container style="max-width: 37rem">
    <v-layout text-xs-center wrap>
      <v-flex xs12>
        <!--context-->
        <v-layout justify-center class="mt-3">
          <h2 class="font-weight-regular">Account</h2>
        </v-layout>
        <v-flex class="ma-2">
          <v-layout justify-end class=" mt-3">
            <h4 class="blue--text font-weight-regular" @click="update">Update 修改</h4>
          </v-layout>
          <v-layout class=" mt-2">
            <h4 class="font-weight-regular">帐号</h4>
          </v-layout>
          <v-layout class=" mt-0">
            <h3 class="font-weight-light">account: {{this.account.nickname}}</h3>
          </v-layout>
          <v-layout class=" mt-2">
            <h4 class="font-weight-regular">状态</h4>
          </v-layout>
          <v-layout>
            <h4 class="font-weight-light">status: {{this.account.status | loginStatusShow}}</h4>
          </v-layout>
          <v-layout class=" mt-2">
            <h4 class="font-weight-regular">邮箱</h4>
          </v-layout>
          <v-layout>
            <h3 class="font-weight-light">Email: {{this.account.mail}}</h3>
          </v-layout>
          <v-layout class=" mt-2 mb-0">
            <h4 class="font-weight-regular">VIP时间</h4>
          </v-layout>
          <v-layout>
            <h3 class="font-weight-light">validity: {{this.account.validity}}</h3>
          </v-layout>
          <v-layout class=" mt-2 pb-0">
            <h4 class="font-weight-regular">我的邀请码</h4>
          </v-layout>
          <v-layout row class="pt-0">
            <h3 class="font-weight-light">Invitation code:</h3>
            <h2 class="orange--text ml-1">{{this.account.id}}</h2>
          </v-layout>
          <v-layout justify-end class="mt-3">
            <h4 class="blue--text font-weight-regular" @click="logout" >Logout 登出</h4>
          </v-layout>

          <v-layout text-xs-left column class="mt-4 mb-2">
            * 所用时间以北美时间为准 当前时间:{{this.serverTime}}
          </v-layout>

          <v-layout class="mt-3">
            <h3 class="font-weight-light">Your referral 您邀请的客户</h3>
          </v-layout>

          <!--显示我的上传记录-->
          <v-layout row wrap class="mt-1 mb-5">
            <!--表头-->
            <v-layout text-xs-left row class="mt-1 mb-1">
              <h4 class="font-weight-regular" style="width: 33%">account</h4>
              <h4 class="font-weight-regular pl-1 text-xs-center" style="width: 10%">referrer</h4>
              <h4 class="font-weight-regular pl-1 text-xs-center" style="width: 27%">logon</h4>
              <h4 class="font-weight-regular text-xs-center" style="width: 30%">VIP</h4>
            </v-layout>
            <!--表体-->
            <v-flex xs12 v-for="(item,index) in this.presenterList" :key="index">
              <v-divider></v-divider>
                <v-layout text-xs-left row block>
                  <h4 class="font-weight-light" style="width: 33%">{{item.nickname}}</h4>
                  <h4 class="font-weight-light pl-1 text-xs-center" style="width: 10%">{{item.presenter}}</h4>
                  <h4 class="font-weight-light pl-1 text-xs-center" style="width: 27%">{{item.logonTime}}</h4>
                  <h4 class="font-weight-light text-xs-center" style="width: 30%">{{item.validity}}</h4>
                </v-layout>
            </v-flex>
          </v-layout>
        </v-flex>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import ethContract from '../../js/accountjs'
export default {
  name: 'Account',
  data: function () {
    return {
      // 变量
      account: '',
      serverTime: '',
      presenterList: [{id: '', nickname: '', mail: '', status: '', validity: '', presenter: '', logonTime: ''}]
    }
  },
  mounted () {
    // 加载时执行
    ethContract.initContract('account')
    this.account = JSON.parse(localStorage.getItem(this.$store.state.accountKey))

    console.log('account', this.account)

    if (this.account !== '' && this.account !== null && this.account.nickname !== '') {
      this.$store.state.loginStatus = true
      this.getPresenterList()
    } else {
      this.$store.state.publicDialog.show = true
      this.$store.state.publicDialog.context = '您还没有登录'
    }

    this.getETHTime()

    console.log('log in ', this.$options.name, this.account)
  },
  filters: {
    loginStatusShow (status) {
      let res = '正常'
      if (status === '2') {
        res = '暂停'
      }
      if (status === '3') {
        res = '失效'
      }
      return res
    }
  },
  methods: {
    // 方法合集

    // 取以太坊时间
    getETHTime () {
      let that = this
      ethContract.getTime()
        .then(function (resolve) {
          // 成功
          let res = JSON.parse(resolve) // 解析json值
          that.serverTime = res.time
        }, function (reject) {
          // 失败
          console.log('getTime reject error', reject)
        })
    },

    // 取服务器时间
    getServerTime () {
      let that = this
      this.$http.get('/time', {timeout: 3000, emulateJSON: true})
        .then(function (response) {
          that.serverTime = response.data
        })
    },
    // 我的邀请列表
    getPresenterList () {
      if (this.account !== null && this.account.id !== '') {
        // 用我的邀请码（id）查询被我邀请的人
        let that = this
        let myID = this.account.id
        ethContract.getMyPresenterCount(myID)
          .then(function (resolve) {
            // 成功
            let res1 = JSON.parse(resolve) // 解析json值
            console.log('getMyPresenterCount', res1)
            if (res1.MyPresenterCount > 0) {
              for (let i = 0; i < res1.MyPresenterCount; i++) {
                ethContract.getPresenterByID(myID, i)
                  .then(function (resolve) {
                    let res = JSON.parse(resolve) // 解析json值
                    console.log('getPresenterByID', res)
                    let presenter = {}
                    presenter.id = res.id
                    presenter.nickname = res.name
                    presenter.logonTime = res.logonTime
                    presenter.presenter = res.presenter
                    presenter.validity = res.validity
                    presenter.status = res.status

                    that.presenterList.push(presenter)
                  })
              }
            }
          })
      }
    },
    // 我的邀请列表
    getPresenterListApi () {
      if (this.account !== null && this.account.id !== '') {
        // 用我的邀请码（id）查询被我邀请的人
        let that = this
        this.$http.get('/UserPresenter/' + that.account.id, {timeout: 3000, emulateJSON: true})
          .then(function (response) {
            let res = response.data
            if (res.length > 0) {
              for (let i = 0; i < res.length; i++) {
                let presenter = {}
                presenter.id = res[i].id
                presenter.nickname = res[i].nickname
                presenter.sysdate = res[i].sysdate
                presenter.mail = res[i].mail
                presenter.description = res[i].description
                presenter.presenter = res[i].presenter

                that.presenterList.push(presenter)
              }
            }
          })
      }
    },
    logout () {
      localStorage.removeItem(this.$store.state.accountKey)
      this.$store.state.loginStatus = false
      let toPage = '/Login'
      this.$router.push({path: toPage})
    },
    goTicket () {
      let toPage = '/Ticket'
      this.$router.push({path: toPage})
    },
    update () {
      let toPage = '/Update'
      let values = this.account.id
      this.$router.push({path: toPage, query: {key: values}})
    }
  },
  beforeDestroy () {
    // 组件结束前执行
  }
}
</script>

<style scoped>

</style>



