import Vue from 'vue'
import Router from 'vue-router'
import uploadFile from '../components/uploadFile.vue'
import HelloWorld from '../components/HelloWorld.vue'
import Detail from '../components/Detail.vue'
import Manage from '../components/UpdateVideo.vue'
import coinMarket from '../components/coinMarket.vue'
import externalWeb from '../components/externalWeb.vue'
import ticket from '../components/Ticket.vue'
import home from '../components/Home.vue'
import logon from '../components/Account/Logon.vue'
import login from '../components/Account/Login.vue'
import account from '../components/Account/Account.vue'
import update from '../components/Account/Update.vue'
import test from '../components/test.vue'

Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
      meta: {
        keepAlive: false // 此组件需要被缓存
      }
    },
    {
      path: '/Detail',
      name: 'Detail',
      component: Detail,
      meta: {
        keepAlive: true // 此组件需要被缓存
      }
    },
    {
      path: '/uploadFile',
      name: 'uploadFile',
      component: uploadFile,
      meta: {
        keepAlive: false // 此组件需要被缓存
      }
    },
    {
      path: '/Manage',
      name: 'Manage',
      component: Manage
    },
    {
      path: '/Logon',
      name: 'Logon',
      component: logon
    },
    {
      path: '/Login',
      name: 'Login',
      component: login
    },
    {
      path: '/Account',
      name: 'Account',
      component: account
    },
    {
      path: '/Update',
      name: 'Update',
      component: update
    },
    {
      path: '/home',
      name: 'home',
      component: home
    },
    {
      path: '/Ticket',
      name: 'Ticket',
      component: ticket
    },
    {
      path: '/coinMarket',
      name: 'coinMarket',
      component: coinMarket
    },
    {
      path: '/externalWeb',
      name: 'externalWeb',
      component: externalWeb
    },
    {
      path: '/test',
      name: 'test',
      component: test
    }
  ]
})



