        <!--<v-card flat tile ripple class="pa-1" style="height: 2rem; width:2rem">-->
          <!--<v-img id="logoTag" ref="logoTag" style="border-radius: 19px; border:1px solid white; cursor: pointer;" aspect-ratio="1" contain @click="viewLogoTag"/>-->
        <!--</v-card>-->
        <v-card flat tile ripple class="pa-1" style="cursor: pointer" @click="goTicket">Issue</v-card>

        <!--<v-card flat tile ripple class="pa-1 ml-2" style="width:12rem; overflow: hidden; white-space: nowrap; text-overflow:ellipsis; cursor: pointer" @click="goLogon">{{this.$store.state.accountShow}}</v-card>        -->
