          <v-icon v-if="this.$store.state.loginStatus" color="blue" class="pa-0">person</v-icon>
          <v-icon v-else>person_outline</v-icon>
        </v-card>
      </v-layout>

      <!--<v-img id="logoTag" ref="logoTag" style="border-radius: 19px; border:1px solid white; cursor: pointer; width: 2rem; height: 2rem" aspect-ratio="1" contain @click="viewLogoTag"/>-->

      <v-menu justify-end bottom>
        <template v-slot:activator="{ on }">
          <v-btn
            left
            icon
            v-on="on"
          >
            <v-icon color="blue-grey">more_vert</v-icon>
          </v-btn>
        </template>
        <v-list>
          <v-list-tile @click="goUploadFile">Upload Video</v-list-tile>
          <v-list-tile @click="goSSCoin">SS Coin</v-list-tile>
          <v-list-tile @click="ssCode">SourceCode</v-list-tile>
          <v-list-tile @click="gitHub">GitHub</v-list-tile>
          <v-list-tile @click="goManage">Manage</v-list-tile>
          <v-list-tile @click="goTicket">Issue</v-list-tile>
          <v-list-tile @click="about">About</v-list-tile>
          <v-list-tile @click="exit">Exit</v-list-tile>
        </v-list>
      </v-menu>

    </v-toolbar>
    <!--<v-content class="pa-0 white" style="background-image: url(../static/image/nature-3054797_1920.jpg)">-->
