      <!--缓冲页面用于在移动端按返回键可以回到前一页 不刷新-->
      <!--<keep-alive>-->
        <!--<router-view v-if="$route.meta.keepAlive">-->
          <!--&lt;!&ndash; 这里是会被缓存的视图组件，比如 page1,page2 &ndash;&gt;-->
          <!--<HelloWorld/>-->
        <!--</router-view>-->
      <!--</keep-alive>-->

      <!--<router-view v-if="!$route.meta.keepAlive">-->
        <!--&lt;!&ndash; 这里是不被缓存的视图组件，比如 page3 &ndash;&gt;-->
        <!--<Detail/>-->
      <!--</router-view>-->

    </v-content>
    <!--分割线-->
    <!--<v-divider></v-divider>-->
    <!--<v-footer class="pa-3">-->
      <!--<v-layout-->
        <!--justify-center-->
        <!--row-->
      <!--&gt;-->
        <!--<span>-->
          <!--<v-layout row>-->
            <!--<v-img height="2rem" width="2rem" src="./static/image/logo-90.png"/>-->
            <!--<span class="mt-1 ml-2">ETHEREUM + IPFS &copy; {{ new Date().getFullYear() }}-babyhard.com</span>-->
            <!--<a href="https://github.com/babyhard/video" target='_blank'>-->
              <!--<v-img height="1.5rem" width="1.5rem" class="ml-3 mt-1" src="./static/image/githubico.jpg"/>-->
            <!--</a>-->
          <!--</v-layout>-->
        <!--</span>-->

      <!--</v-layout>-->
    <!--</v-footer>-->
    <!--在屏幕中间显示logoTag大图-->
    <!--<v-dialog v-model="showBigLogoTag" hide-overlay persistent width="11rem">-->
      <!--<v-img id="logoTagBig" ref="logoTagBig" aspect-ratio="1" style="border-radius: 9px; border:1px solid orangered;" contain/>-->
    <!--</v-dialog>-->

    <!--公用提示对话框-->
    <v-dialog
      v-model="this.$store.state.publicDialog.show"
      persistent
      width="370"
    >
      <v-card>
        <v-card-title class="pa-1 grey lighten-2">
          {{this.$store.state.publicDialog.title}}
          <v-spacer></v-spacer>
          <v-icon @click="closePublicDialog">close</v-icon>
        </v-card-title>

        <v-card-text>
          {{this.$store.state.publicDialog.context}}
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            flat
            color="green darken-1"
            @click="closePublicDialog"
          >
            close
