    <!--<router-view/>-->
  <!--</div>-->
</template>

<script>
import HelloWorld from './components/HelloWorld'
import Detail from './components/Detail'
export default {
  name: 'App',
  account: '',
  components: {Detail, HelloWorld},
  data: function () {
    return {
      // 节气图标(暂弃)
      showBigLogoTag: false,
      logoTagData: '',
      logoTagDef: './static/image/logoTag.png' // 节日图标默认本地图标路径
    }
  },
  mounted () {
    this.getAccount()
  },
  methods: {
    // 取帐号信息
    getAccount () {
      this.account = JSON.parse(localStorage.getItem(this.$store.state.accountKey))
      // 判断登录状态
      if (this.account !== '' && this.account !== null && this.account.nicknam !== '') {
        this.$store.state.loginStatus = true
      }
    },
    home () {
      let toPage = '/Home'
      this.$router.push({path: toPage})
    },
    closePublicDialog () {
      this.$store.state.publicDialog.show = false
    },
    // 显示节气图标
    getFestivalTag () {
      let that = this
      // 在githtb中更不同节日图标 为logoTag
      this.$http.get('https://raw.githubusercontent.com/bliface/video/master/festival/logoTag.png', {responseType: 'blob'})
        .then(function (response) {
          that.logoTagData = response.data

          let reader = new FileReader()
          reader.onload = (e) => {
            that.$refs.logoTag.src = e.target.result
          }
          reader.readAsDataURL(that.logoTagData)
        })
        .catch(function (error) {
          that.$refs.logoTag.src = that.logoTagDef
          console.log('getFestivalTag error', error)
        })
    },
    viewLogoTag () {
      if (this.showBigLogoTag) {
        this.showBigLogoTag = false
        return
      }

      // let logoTagBig = document.getElementById('logoTagBig')
      if (this.logoTagData !== '') {
        console.log('viewLogoTag', this.logoTagData)
        // this.$refs.logoTagBig.src = this.logoTagImg

        let reader = new FileReader()
        reader.onload = (e) => {
          this.$refs.logoTagBig.src = e.target.result
          // console.log('getFestivalTag2', that.logoTagData)
        }
        reader.readAsDataURL(this.logoTagData)
      } else {
        this.$refs.logoTagBig.src = this.logoTagDef
      }

      this.showBigLogoTag = true

      let that = this
      // 三秒后关闭提示
      setTimeout(function () {
        that.showBigLogoTag = false
      }, 1000 * 5)
    },
    goBack () {
      if (this.$router.currentRoute.path !== '/') {
        this.$router.back()
      } else {
        this.$router.back()
        window.plus.runtime.quit()
      }
    },
    goHome () {
      // let url = this.$store.state.website
      //
      // if (window.location.protocol === 'http:') {
      //   url = 'http://bliface.com'
      // }
      // window.open(url)

      let toPage = '/'
      // 同一页面不跳转
      // if (window.location.href.indexOf(toPage) > 0) {
      //   return
      // }

      this.$router.push({path: toPage})
    },
    goUploadFile () {
      // this.$router.push({path: '/Detail', query: {hash: videoHash}})

      let toPage = '/uploadFile'
      // 同一页面不跳转
      if (window.location.href.indexOf(toPage) > 0) {
        return
      }

      if (window.location.protocol === 'https:') {
        this.$store.state.publicDialog.show = true
        this.$store.state.publicDialog.context = 'upload video need http Protocol\nplease use http://bliface.com'
        return
      }

      // 上传界面在https协议下会报错 强制修改为http协议中进行 （需在服务器端改善)
      this.$router.push({path: toPage})

      console.log('go: ', toPage)
    },
    goTicket () {
      let toPage = '/Ticket'
      this.$router.push({path: toPage})
    },
    goManage () {
      let toPage = '/Manage'
      this.$router.push({path: toPage})
    },
    goLogon () {
      // 如果没有登录 就转去登录界面
      this.account = JSON.parse(localStorage.getItem(this.$store.state.accountKey))
      if (this.account !== '' && this.account !== null && this.account.nickname !== '') {
        let toPage = '/Account'
        this.$router.push({path: toPage})
      } else {
        let toPage = '/Login'
        this.$router.push({path: toPage})
      }
    },
    goSSCoin () {
      let toPage = '/coinMarket'
      this.$router.push({path: toPage})
      // // 同一页面不跳转
      // if (window.location.href.indexOf(toPage) > 0) {
      //   return
      // }
      //
      // let routeData = this.$router.resolve({
      //   path: toPage
      // })
      //
      // window.open(routeData.href) // 要在新窗口打开加 '_blank'
      console.log('go: ', toPage)
    },
    ssCode () {
      let url = 'https://github.com/bliface/video/blob/master/SS%20Source%20Code.sol'
      this.$router.push({path: '/externalWeb', query: {webUrl: url}})
    },
    ssWhiteBook () {
      let url = 'https://github.com/bliface/video/blob/master/ss%20whitebook.pdf'
      this.$router.push({path: '/externalWeb', query: {webUrl: url}})
    },
    gitHub () {
      let url = 'https://github.com/bliface/video'
      this.$router.push({path: '/externalWeb', query: {webUrl: url}})
    },
    about () {
      let url = 'https://github.com/bliface/video/blob/master/README.md'
      this.$router.push({path: '/externalWeb', query: {webUrl: url}})
    },
    exit () {
      window.plus.runtime.quit()
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>



