<template>
    <v-container>
        <v-layout text-xs-center wrap>

                <!--context-->
              <v-layout row wrap>
              <v-flex xs12 sm6>
                <v-checkbox
                  v-model="mode"
                  label="Multi-line (mobile)"
                  value="multi-line"
                ></v-checkbox>
              </v-flex>

              <v-flex xs12 sm6>
                <v-checkbox
                  v-model="mode"
                  label="Vertical (mobile)"
                  value="vertical"
                ></v-checkbox>
              </v-flex>
              </v-layout>

        </v-layout>
    </v-container>
</template>

<script>
import contratABI from '../js/Ethereum'

export default {
  name: 'test',
  data: () => ({
    // 变量
  }),
  mounted () {
    // 加载时执行
    console.log('log in ', this.$options.name)

    contratABI.initContract(this.$store.state.sysModel)

    // 加载视频图片列表
    contratABI.initVideoList(9000)
  },
  methods: {
    // 方法合集
  },
  beforeDestroy () {
    // 组件结束前执行
  }
}
</script>

<style scoped>

</style>



