<template>
  <v-container style="max-width: 80rem">
    <v-layout text-xs-center wrap>
      <v-flex xs12 mb-5>
        <!--context-->
        <v-layout class="ma-2" row>
          <v-flex>
            <v-btn color="blue-grey" class="white--text" onclick="selectVideo.click()" >video file</v-btn>
          </v-flex>
          <v-flex xs10>
            <input style="width: 100%; height: 100%;" type="text" placeholder="MP4 only max size 50M"  id="selectVideoPath" ref="selectVideoPath" readonly="readonly" v-model="selectVideoPath">
          </v-flex>
          <v-spacer></v-spacer>
          <v-flex>
            <v-btn flat @click="showVideoSwitch">{{ showVideo | hideShowText }}</v-btn>
          </v-flex>
        </v-layout>
        <v-layout justify-center  class="green lighten-4 pa-1">
          <video v-show="showVideo" id="player" ref="player" width="100%" height="100%" type="video/mp4" controls>
            <input
              id="selectVideo"
              ref="selectVideo"
              type="file"
              style="display:none"
              @change="selectVideo($event)"
              accept="video/mp4"
            />
          </video>
        </v-layout>
        <v-layout row justify-end class="mr-2">
          <v-btn flat @click="showVideoSwitch">{{ showVideo | hideShowText }}</v-btn>
        </v-layout>
        <v-layout class="mt-2" row>
          <h3 class="mr-2">Video cover</h3>
          <v-label>[640x360 jpg only, max 200k]</v-label>
          <h3 style="text-decoration: underline; cursor: pointer; color: blue" class="ml-2" @click="getVideoImg" >GetVideoImg</h3>
        </v-layout>
        <v-flex xs2>
          <v-img id="videoImg" ref="videoImg" contain height="90" :src=videoImgSrc aspect-ratio="1.5" onclick="selectVideoImg.click()">
            <input
              id="selectVideoImg"
              ref="selectVideoImg"
              type="file"
              @change="selectImg($event)"
              style="display: none"
              accept="image/jpeg"
            />
          </v-img>
        </v-flex>
        <v-layout class="mt-3">
          <v-text-field ref="VMVideoTitle" label="TITLE max 32 byte" maxLength="30" clearable hide-details append-icon="edit" v-model="inputTitle"/>
        </v-layout>
        <v-layout class="mt-3">
          <v-text-field label="TAG" maxLength="30"  @click:clear="clearTagArray" clearable hide-details append-icon="edit" v-model="inputTag"/>
        </v-layout>
        <!--checkbox array-->
        <v-layout row wrap>
          <v-checkbox v-model="tagArray" label="AI" color="blue-grey" value="AI"></v-checkbox>
          <v-checkbox v-model="tagArray" label="Cartoon" color="blue-grey" value="Cartoon"></v-checkbox>
          <v-checkbox v-model="tagArray" label="First" color="blue-grey" value="First"></v-checkbox>
          <v-checkbox v-model="tagArray" label="Gay" color="blue-grey" value="Gay"></v-checkbox>
          <v-checkbox v-model="tagArray" label="Group" color="blue-grey" value="Group"></v-checkbox>
          <v-checkbox v-model="tagArray" label="Japan" color="blue-grey" value="Japan"></v-checkbox>
          <v-checkbox v-model="tagArray" label="Self" color="blue-grey" value="Self"></v-checkbox>
          <v-checkbox v-model="tagArray" label="SM" color="blue-grey" value="SM"></v-checkbox>
          <v-checkbox v-model="tagArray" label="Student" color="blue-grey" value="Student"></v-checkbox>
          <v-checkbox v-model="tagArray" label="Teen" color="blue-grey" value="Teen"></v-checkbox>
          <v-checkbox v-model="tagArray" label="Toy" color="blue-grey" value="Toy"></v-checkbox>
        </v-layout>
        <v-layout row wrap>
          <v-checkbox v-model="tagArray" label="Beautiful" color="blue-grey" value="Beautiful"></v-checkbox>
          <v-checkbox v-model="tagArray" label="Baby" color="blue-grey" value="Baby"></v-checkbox>
          <v-checkbox v-model="tagArray" label="Cam" color="blue-grey" value="Cam"></v-checkbox>
          <v-checkbox v-model="tagArray" label="Crazy" color="blue-grey" value="Crazy"></v-checkbox>
          <v-checkbox v-model="tagArray" label="SuperStar" color="blue-grey" value="SuperStar"></v-checkbox>
          <v-checkbox v-model="tagArray" label="Monster" color="blue-grey" value="Monster"></v-checkbox>
          <v-checkbox v-model="tagArray" label="Tit" color="blue-grey" value="Tit"></v-checkbox>
          <v-checkbox v-model="tagArray" label="Teach" color="blue-grey" value="Teach"></v-checkbox>
        </v-layout>

        <v-layout class="mt-3">
          <v-text-field maxLength="42"
                        label="Your Ethereum account (optional)"
                        clearable
                        v-model="inputMyAccount"
          ></v-text-field>
          <v-icon @click="getMetaMaskAccount" class="cursor: pointer" medium>refresh</v-icon>
        </v-layout>

        <v-layout class="mt-3">
          <v-radio-group v-model="inputCost" row>
            <v-radio value="free" label="free"/>
            <v-radio value="vip" label="vip"/>
          </v-radio-group>
        </v-layout>
        <v-layout justify-center class="mt-5">
          <v-btn round color="blue-grey" class="white--text" @click="upLoadFile" >uploadFile <v-icon right dark>cloud_upload</v-icon></v-btn>
          <!--<v-btn round color="info" @click="initVideo" >temp</v-btn>-->
          <!--<v-btn round color="info" @click="test" >test</v-btn>-->
          <!--<v-btn round color="info" @click="test4" >test4</v-btn>-->
          <!--<v-btn round color="info" @click="test6" >test6</v-btn>-->
          <!--<v-img ref="testImg">img</v-img>-->
        </v-layout>

        <!--<v-layout justify-center class="mt-5 mb-1" >-->
        <!--<v-label>Upload file need your run IPFS daemon and ethereum MetaMask in your computer</v-label>-->
        <!--</v-layout>-->
        <v-divider class="mt-5"/>
        <v-layout justify-center class="mt-1">
          <v-label>my upload record: {{this.$store.state.myUploadFileList.length}}</v-label>
        </v-layout>

        <!--显示我的上传记录-->
        <v-layout row wrap class="mt-5">
          <v-flex xs12 v-for="(item,index) in this.$store.state.myUploadFileList" :key="index">
            <v-card class="mt-2">
              <v-layout text-xs-left d-flex row wrap class="body-2">
                <v-flex xs2>
                  <!--<v-img src="./static/image/t1.jpg"/>-->
                  <v-img height="8rem" aspect-ratio="4/3" :src="`${item.imgPath}`" :lazy-src="`${item.imgPath}`"/>
                </v-flex>
                <v-flex xs6 class="ml-3">
                  <v-layout class="mt-2">
                    <span>hash: {{item.videoHash}}</span>
                  </v-layout>
                  <v-layout>
                    <span>title: {{item.title}}</span>
                  </v-layout>
                  <v-layout>
                    <span>tags: {{item.tags}}</span>
                  </v-layout>
                  <v-layout>
                    <span>ID: {{item.time}}</span>
                  </v-layout>
                </v-flex>
                <v-flex xs4 class="ml-5">
                  <v-layout class="mt-2 subheading">
                    <!--根据变量值显示不同颜色-->
                    <!--<span :style="'color:'+(item.status=='pending'?'orange':'blue')+';'">status: {{item.status}}</span>-->
                    <span :style="'color:'+(item.status=='pending'?'orange' : '')+';'">status: {{item.status|myVideoShowStatus}}</span>
                  </v-layout>
                  <v-layout>
                    <span>praise {{item.praise}}</span>
                  </v-layout>
                </v-flex>
              </v-layout>
            </v-card>
          </v-flex>
        </v-layout>
      </v-flex>
      <!--在屏幕中间显示 共用等待框-->
      <v-dialog v-model="this.$store.state.waitDialog" hide-overlay persistent width="210">
        <v-card color="primary" dark>
          <v-card-text>
            Please stand by
            <v-progress-circular
              indeterminate
              :width="2"
              color="white"
              class="mb-0"
            ></v-progress-circular>
          </v-card-text>
        </v-card>
      </v-dialog>
    </v-layout>
  </v-container>
</template>

<script>
import videojs from 'video.js'
import ethContract from '../js/Ethereum'
import ipfsApi from 'ipfs-api'
import localDataApi from '../js/localDataApi'

export default {
  name: 'uploadFile',
  data: function () {
    return {
      // 变量
      protocol: 'http', // 当前的网络协议
      inputTitle: '',
      inputTag: '',
      tagArray: [],
      inputMyAccount: '',
      inputCost: 'free',
      selectVideoPath: '',
      barVal: 0,
      num: 1,
      myPlayer: '',
      videoImgSrc: '',
      showVideo: true
    }
  },

  beforeCreate () {
    // 上传界面在https协议下会报错 强制修改为http协议中进行 （需在服务器端改善)
    // window.location.protocol = 'http:'

    // 判断当前的网络协议 默认http
    this.protocol = window.location.protocol.split(':')[0]
  },
  mounted () {
    console.log('log in', this.$options.name, this.protocol)

    this.clearPageMeta()
    // 加载时初始化智能合约
    ethContract.initContract(this.$store.state.sysModel)

    // 加载我的文件上传列表
    this.loadMyFile()
  },
  watch: {
    // 监视字符串数组长度不超32
    tagArray: function () {
      let alength = 0
      for (let i = 0; i < this.tagArray.length; i++) {
        alength += this.tagArray[i].length + 1 // 1为分隔符长度
      }
      // console.log('tagArray length', alength)
      if (alength < 30) {
        this.inputTag = this.tagArray
      }
    },
    inputTitle: function () {
      // 长度不超32
      let maxLength = 30
      let sl = this.$global.getStringByteLength(this.inputTitle)
      if (sl > maxLength) {
        this.inputTitle = this.$global.subStringByte(this.inputTitle, maxLength)
      }
    }
  },
  computed: {
    // getImgByte: function (item) {
    //   return this.videoInfo[item].imgByte
    // }
  },
  filters: {
    // 用于条件显示文本插值

    // 上传列表
    myVideoShowStatus (status) {
      let res = 'normal' // 默认值1时显示
      if (status === 'pending') {
        res = 'pending'
      }
      if (status === 0) {
        res = 'disabled'
      }
      return res
    },

    // 上传视频显示开关文本
    hideShowText (status) {
      let res = 'hide' // 默认值1时显示
      if (!status) {
        res = 'show'
      }
      return res
    }
  },
  methods: {
    // 方法合集
    showVideoSwitch () {
      if (this.showVideo) {
        this.showVideo = false
      } else {
        this.showVideo = true
      }
      window.scrollTo(0, 0) // 滚动到顶部
    },

    initVideo (src) {
      // 初始化视频方法
      let vmPlayer = this.$refs.player
      this.myPlayer = videojs(vmPlayer, {
        // 确定播放器是否具有用户可以与之交互的控件。没有控件，启动视频播放的唯一方法是使用autoplay属性或通过Player API。
        controls: true,
        // 自动播放属性,muted:静音播放
        autoplay: 'muted',
        // 建议浏览器是否应在<video>加载元素后立即开始下载视频数据。
        preload: 'auto'
      })

      // myPlayer.src({src: '../../static/video/test.mp4', type: 'video/mp4'})
      this.myPlayer.src({src: src, type: 'video/mp4'})
      console.log('initVideo', this.myPlayer.src())
    },
    test () {
      localDataApi.initDB()
      let openDBpromise = localDataApi.openDB()
      openDBpromise.then(function (resolve) {
        // 成功
        console.log('openDBpromiseResolve', resolve.result)
      }, function (reject) {
        // 失败
        console.log('openDBpromiseReject', reject)
      })

      // let key = 'key1'
      // let data = {Key: key, stu: 'value1', dfd: 'fdsfas1'}
      // let putPromise = localDataApi.put('student', data)
      // putPromise.then(function (resolve) {
      //   // 成功
      //   console.log('putPromiseResolve', resolve)
      // }, function (reject) {
      //   // 失败
      //   console.log('putPromiseReject', reject)
      // })

      let getPromise = localDataApi.get('student', 3, 'dd')
      getPromise.then(function (resolve) {
        // 成功
        console.log('getPromiseResolve', resolve)
      }, function (reject) {
        // 失败
        console.log('getPromiseReject', reject)
      })
    },
    getMetaMaskAccount () {
      // 检查metamask获取当前网络和帐号
      if (typeof window.web3 !== 'undefined') {
        // 检查帐号和网络
        let that = this
        // console.log('ccounts', that.$store.state.contractNetworkByDataFile, window.web3, that.$store.state.myNetwork)
        window.web3.version.getNetwork(function (error, response) {
          if (error === null) {
            if (response === that.$store.state.contractNetworkByDataFile) {
              if (typeof window.web3.eth.defaultAccount !== 'undefined') {
                that.inputMyAccount = window.web3.eth.defaultAccount
              } else {
                that.$store.state.publicDialog.show = true
                that.$store.state.publicDialog.context = 'please login MetaMask'
              }
            } else {
              let networkHint = 'Main'
              if (that.$store.state.contractNetworkByDataFile === '4') {
                networkHint = 'Rinkeby'
              }
              that.$store.state.publicDialog.show = true
              that.$store.state.publicDialog.context = 'please select MetaMask network in ' + networkHint
            }
            // console.log('response', response, window.web3.eth.defaultAccount, Store.state.myAccount)
          }
        })
      } else {
        this.$store.state.publicDialog.show = true
        this.$store.state.publicDialog.context = 'Not MetaMask in your Browser'
      }
    },
    clearPageMeta () {
      // 清理页面数据
      if (this.$store.state.myUploadFileList.length > 0) {
        this.$store.state.myUploadFileList.slice(0, this.$store.state.myUploadFileList.length)
      }
    },
    goTest () {
      console.log('go test: ')
      // this.$router.push({path: '/Detail', query: {hash: videoHash}})
      let routeData = this.$router.resolve({
        path: '/test'
      })
      window.open(routeData.href, '_blank') // 新窗口打开
    },

    temp () {
      this.$global.findMyUploadFileList2('QmfQs7cQ6BKoLeSi9KnjcQBMAzNcCfhXkMWcZPRqpjC3eW')
    },

    /**
     * 清理值的同时清理多选框
     * */
    clearTagArray () {
      this.tagArray = []
    },

    // 加载我的上传记录
    loadMyFile () {
      // 检查metamask获取当前网络和帐号
      if (typeof window.web3 !== 'undefined') {
        // 检查帐号和网络
        let that = this
        window.web3.version.getNetwork(function (error, response) {
          if (error === null) {
            console.log('initMetaMask', response, that.inputMyAccount, window.web3.eth.defaultAccount)
            that.$store.state.myNetwork = response
            if (that.$store.state.contractNetworkByDataFile === response) {
              if (window.web3.eth.defaultAccount !== null && typeof window.web3.eth.defaultAccount !== 'undefined') {
                that.$store.state.myAccount = window.web3.eth.defaultAccount
                that.inputMyAccount = that.$store.state.myAccount
                ethContract.getMyFileList(that.inputMyAccount)
              }
            }
          }
        })
      }
    },

    // 上传视频
    upLoadFile () {
      const selectVideo = this.$refs.selectVideo
      const videoImg = this.$refs.videoImg
      // console.log('select file', videoImg, selectVideo.files[0])
      // selectVideoImg.value 路径；selectVideoImg.files[0].name文件名

      if (window.location.protocol === 'https:') {
        this.$store.state.publicDialog.show = true
        this.$store.state.publicDialog.context = 'upload video need http Protocol\nplease use http://babyhard.com'
        return
      }

      if (typeof videoImg.src === 'undefined') {
        this.$store.state.publicDialog.show = true
        this.$store.state.publicDialog.context = 'please select video img file'
        return
      }

      if (typeof selectVideo.files[0] === 'undefined') {
        this.$store.state.publicDialog.show = true
        this.$store.state.publicDialog.context = 'please select video file'
        return
      }

      if (this.inputTitle === '') {
        this.$store.state.publicDialog.show = true
        this.$store.state.publicDialog.context = 'please input video title'
        return
      }

      // if (this.inputTag === '') {
      //   alert('please input video tag')
      //   return
      // }

      if (this.inputMyAccount.length !== 0 && this.inputMyAccount.length !== 42) {
        this.$store.state.publicDialog.show = true
        this.$store.state.publicDialog.context = 'please input valid ethereum account'
        return
      }

      let videoFile = selectVideo.files[0]

      // 判断浏览器是否支持 FileReader
      if (!window.FileReader) {
        this.$store.state.publicDialog.show = true
        this.$store.state.publicDialog.context = '您的设备不支持图片预览功能，如需该功能请升级您的设备！'
        return
      }

      // 检测是否重复提交
      if (this.$store.state.myUploadFileList.length > 0) {
        for (let i = 0; i < this.$store.state.myUploadFileList.length; i++) {
          if (this.$store.state.myUploadFileList[i].videoFile === videoFile) {
            this.$store.state.publicDialog.show = true
            this.$store.state.publicDialog.context = 'Do not repeat the upload file'
            return
          }
        }
      }

      // 上传视频
      let that = this
      that.$store.state.waitDialog = true // 显示等待框
      let videoReader = new FileReader()

      // reader.readAsDataURL(uploadFile)
      videoReader.readAsArrayBuffer(videoFile)
      videoReader.onload = function () {
        let videoBuffer = Buffer.from(new Uint8Array(videoReader.result))

        console.log('upLoad File buffer length', videoBuffer.length, that.$store.state.ipfsHost.server)

        if (videoBuffer.length > 1) {
          const myIpfs = ipfsApi(that.$store.state.ipfsHost.server, that.$store.state.ipfsHost.port, {protocol: that.protocol})

          // 上传视频
          myIpfs.add(videoBuffer)
            .then(function (response2) {
              let videoHash = response2[0].hash
              // console.log('videoBuffer', response2)
              // 上传视频图片
              let imgBlob = that.base64ToBlob(videoImg.src)
              let videoImgReader = new FileReader()
              videoImgReader.readAsArrayBuffer(imgBlob)
              videoImgReader.onload = function () {
                let videoImgBuffer = Buffer.from(new Uint8Array(videoImgReader.result))
                myIpfs.add(videoImgBuffer)
                  .then(function (response3) {
                    let imgHash = response3[0].hash

                    // console.log('videoImgBuffer', response3)
                    // 如果没有上传帐号，则默认为系统帐号
                    let author = that.inputMyAccount
                    if (author === '' || typeof author === 'undefined') {
                      author = that.$store.state.sysAuthor
                    }

                    // 视频属性写入ETH
                    let fileItem = {
                      'videoFile': videoFile,
                      'videoHash': videoHash,
                      'imgHash': imgHash,
                      'author': author,
                      'category': '1:video', // 按josn格式定义多属性（1为视频大类）
                      'title': that.inputTitle,
                      'tags': that.inputTag,
                      'price': that.inputCost, //  视频价格待定义 可用逗号边接多个属性(1视频类型，2基本货币单位，3价格, VIP有效期)
                      'memo': '',
                      'status': 'pending',
                      'imgPath': that.$store.state.myGatewayHash + imgHash
                    }

                    console.log('upload video hash', fileItem.videoHash, '\nimgHash', fileItem.imgHash, '\nauthor', that.inputMyAccount, that.inputTitle, that.inputTag)

                    // ethContract.pushVideo(videoHash, videoImgHash, that.$store.state.sysAuthor, _category, that.inputTitle, that.inputTag, _price)
                    ethContract.pushVideo(fileItem.videoHash, fileItem.imgHash, fileItem.author, fileItem.category, fileItem.title, fileItem.tags, fileItem.price, fileItem.memo)
                    // that.test3(fileItem.videoHash, fileItem.imgHash)
                    that.$store.state.myUploadFileList.unshift(fileItem)
                    that.$store.state.waitDialog = false // 消失等待框
                  })
                  .catch(function (error) {
                    console.log('upload video img error', error)
                    that.$store.state.waitDialog = false // 消失等待框
                  })
              }
            })
            .catch(function (error) {
              that.$store.state.publicDialog.show = true
              that.$store.state.publicDialog.context = 'upload video error\n' + error
              that.$store.state.waitDialog = false // 消失等待框
            })
        } else {
          that.$store.state.publicDialog.show = true
          that.$store.state.publicDialog.context = 'not buffer content'
          that.$store.state.waitDialog = false // 消失等待框
        }
      }
    },

    // 选择频文件
    selectVideo (e) {
      let selectFile = e.target

      // 判断浏览器是否支持 FileReader
      if (!e || !window.FileReader) {
        this.$store.state.publicDialog.show = true
        this.$store.state.publicDialog.context = '您的设备不支持图片预览功能，如需该功能请升级您的设备！'
        return
      }

      // 检查文件类型必须是mp4
      if (selectFile.value.substring(selectFile.value.length - 3) !== 'mp4') {
        this.$store.state.publicDialog.show = true
        this.$store.state.publicDialog.context = 'video file must be mp4 only'
        return
      }
      // 检查文件大小不超51m, 在文件切割时应不大于49M
      if ((selectFile.files[0].size / 1024) > (1024 * 51)) {
        this.$store.state.publicDialog.show = true
        this.$store.state.publicDialog.context = 'file max size 50M'
        return
      }
      let reader = new FileReader()
      // 设置文件
      // console.log('selectFile', selectFile, '\nreader', reader, '\npath', selectFile.value, selectFile.files[0].name)
      // 这里是最关键的一步，转换就在这里
      if (selectFile.files[0]) {
        this.$store.state.waitDialog = true // 显示等待框
        this.waitDialog = true
        // let vmPlayer = this.$refs.player
        // let vmVideoImg = this.$refs.videoImg

        let vmPlayer = document.getElementById('player')

        reader.readAsDataURL(selectFile.files[0])
        let that = this
        reader.onload = function () {
          // 处理读取的结果
          // let buffer = Buffer.from(reader.result)
          // player.src = buffer

          vmPlayer.src = reader.result
        }
        // 截取视频第一帧为视频封面
        vmPlayer.addEventListener('canplay', function captureImage () {
          if (vmPlayer.videoHeight < 360) {
            that.$store.state.publicDialog.show = true
            that.$store.state.publicDialog.context = 'The video resolution is too low, than more than 360'
            that.$store.state.waitDialog = false
            return
          }

          that.$global.sleep(2)

          let canvas = document.createElement('canvas')
          // 计算视频封面缩放比例 标准为h360, w640
          let scaling = Math.round((360 / vmPlayer.videoHeight) * 100) / 100 // 四舍五入保留两位小数，截取小数位用toFixed(2)
          canvas.width = vmPlayer.videoWidth * scaling
          canvas.height = vmPlayer.videoHeight * scaling

          canvas.getContext('2d').drawImage(this, 0, 0, canvas.width, canvas.height)

          // 设置动态添加的元素的样式
          // viewImg.className = 'all'
          // viewImg.crossOrigin = 'anonymous'
          // alert(canvas.toDataURL("image/png"))
          let canvasSrc = canvas.toDataURL('image/jpeg')
          // let canvasBuffer = Buffer.from(canvasSrc)
          that.videoImgSrc = canvasSrc

          // that.videoImgSrc = canvasSrc
          // let videoImg = document.getElementById('videoImg')
          // that.$refs.videoImg.src = canvasSrc
          that.selectVideoPath = e.target.value // 显示路径

          let fileName = selectFile.files[0].name.substr(0, selectFile.files[0].name.length - 4).trim() // 文件名（除扩展名）

          // that.inputTitle = selectFile.files[0].name.substring(0, selectFile.files[0].name.length - 4).substring(0, 32).trim()
          // 从文件名尾部截取32个字节
          that.inputTitle = that.$global.subStringByteDesc(fileName, 30)
          that.$store.state.waitDialog = false
          // console.log('canvasSrc', fileName, that.inputTitle)
        })
      }
    },

    // 手动获取视频当前贞图片
    getVideoImg () {
      let vmPlayer = document.getElementById('player')

      if (vmPlayer.src === '') {
        this.$store.state.publicDialog.show = true
        this.$store.state.publicDialog.context = 'Invalid video'
        return
      }
      if (vmPlayer.videoHeight < 360) {
        this.$store.state.publicDialog.show = true
        this.$store.state.publicDialog.context = 'The video resolution is too low, than more than 360'
        return
      }

      let canvas = document.createElement('canvas')
      // 计算视频封面缩放比例 标准为h360, w640
      let scaling = Math.round((360 / vmPlayer.videoHeight) * 100) / 100 // 四舍五入保留两位小数，截取小数位用toFixed(2)
      canvas.width = vmPlayer.videoWidth * scaling
      canvas.height = vmPlayer.videoHeight * scaling

      canvas.getContext('2d').drawImage(vmPlayer, 0, 0, canvas.width, canvas.height)
      let canvasSrc = canvas.toDataURL('image/jpeg')
      // let canvasBuffer = Buffer.from(canvasSrc)
      this.$refs.videoImg.src = canvasSrc

      // console.log('getVideoImg', canvas.width, canvas.height, canvasSrc)
    },

    // 将base64转换为blob
    base64ToBlob (dataurl) {
      let arr = dataurl.split(',')
      let mime = arr[0].match(/:(.*?);/)[1]
      let bstr = atob(arr[1])
      let n = bstr.length
      let u8arr = new Uint8Array(n)
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n)
      }
      return new Blob([u8arr], {type: mime})
    },

    // 将blob转换为file
    blobToFile (theBlob, fileName) {
      theBlob.lastModifiedDate = new Date()
      theBlob.name = fileName
      return theBlob
    },

    selectImg (e) {
      let selectFile = e.target
      // 图片不大于200k
      if (this.$global.verificationFileSize(selectFile, 200) === '0') {
        return
      }

      // 判断浏览器是否支持 FileReader
      if (!e || !window.FileReader) {
        this.$store.state.publicDialog.show = true
        this.$store.state.publicDialog.context = '您的设备不支持图片预览功能，如需该功能请升级您的设备！'
        return
      }
      let reader = new FileReader()
      // 设置文件

      // 这里是最关键的一步，转换就在这里
      if (selectFile.files[0]) {
        // console.log('selectFile', selectFile, '\npath', selectFile.value, selectFile.files[0].name, reader.result)

        this.$global.verificationPicDimension(selectFile, 640, 360)

        let viewImg = this.$refs.videoImg

        reader.onload = function (e) {
          let imgData = e.target.result

          viewImg.src = imgData

          // console.log('img', imgData.naturalWidth, imgData.width, viewImg.scrollWidth, viewImg.clientWidth)
        }

        reader.readAsDataURL(selectFile.files[0])
      }
    },

    fileReaderToBuffer (bufferString) {
      let fileSize = bufferString.length
      this.num = 1
      const blockSize = 1 * 1024 * 1024
      let nextSize = Math.min(this.num * blockSize, fileSize)

      let buffer = Buffer.from(bufferString)
      console.log('\nreader selectImg', fileSize, nextSize, buffer)

      let fileBuffer = ''
      // 大于1M，分段上传
      while (fileSize > nextSize) {
        nextSize = Math.min(this.num * blockSize, fileSize)
        const slice = bufferString.slice((this.num - 1) * blockSize, nextSize)
        // const percent = (blockSize * this.num) / fileSize
        // 上传出错则重新上传
        try {
          fileBuffer += Buffer.from(slice)
          // console.log('splitFile2', fileBuffer, nextSize, this.num)
          // response = await this.onUpload2(slice, file, md5code, percent)
        } catch (error) {
          // response = await this.onUpload2(slice, file, md5code, percent)
        }
        this.num++
      }

      return fileBuffer
    }
  },
  beforeDestroy () {
    // 组件结束前执行
  }
}
</script>

