function findABIBySymbol (_symbol) {
  for (var i = 0; i < ABIs.length; i++) {
    if (ABIs[i].symbol === _symbol) {
      return {
        symbol: ABIs[i].symbol,
        host: ABIs[i].host,
        address: ABIs[i].address,
        functionHashes: ABIs[i].functionHashes,
        abi: ABIs[i].abi
      }
    }
  }
  return ''
}

export default {findABIBySymbol, ABIs}



