// 初始化公共网关, 如果默认公共网关不在线则从网关列表选择在线网关
function initPublicGateway () {
  axios.get(Store.state.publicGateway + Store.state.testHashStr, {timeout: 5000})
    .then(function (response) {
      console.log('ininGateways', response)
    })
    .catch(function (error) {
      console.log('init online Gateways', error)
      getOnlinePublicGateway(Store.state.testHashStr)
    })
}

function serverHeader () {
  vm.$http.get('/time', {emulateJSON: true})
    .then(function (response) {
      let date1 = new Date(response.data).getTime()
      let date2 = global.getFormatDateByTimestamp(date1, 'yyyy-MM-dd hh:mm:ss')
      let date3 = new Date(date2).getTime()
      console.log('serverHeader ', response.data, date1, date2, date3)
    })
}

// 从网关列表中设置在线的网关
function getOnlinePublicGateway (testHash) {
  let gateways = Store.state.gateways
  Store.state.gateway = '' // 重置风关
  for (let i = 0; i < gateways.length; i++) {
    if (Store.state.gateway !== '') {
      return
    }
    axios.get(gateways[i] + testHash, {timeout: 3000})
      .then(function (response) {
        // console.log('gatways', gateways[i], response, response.status)

        if (Store.state.gateway === '' && response.statusText === 'OK') {
          Store.state.gateway = gateways[i]
          console.log('onlinegateways', Store.state.gateway)
        }
      })
  }
}

// 初始指定合约
function initContract (contractSymbol) {
  // 检查当前token是否存在
  contractInfo = myAbi.findABIBySymbol(contractSymbol)
  if (typeof contractInfo.abi === 'undefined') {
    Store.state.publicDialog.show = true
    Store.state.publicDialog.context = 'not find token'
    console.log('not find token' + contractSymbol)
    return
  }

  // 设置数据文件所在网络
  Store.state.contractNetworkByDataFile = contractInfo.host
  let contractProvider
  if (contractInfo.host === '1') {
    contractProvider = new Eth(new Web3.providers.HttpProvider(Store.state.mainInfura))
  } else {
    contractProvider = new Eth(new Web3.providers.HttpProvider(Store.state.rinkebyInfura))
    // contractProvider = new Eth(window.web3.currentProvider)
  }

  let ethContract = new EthContract(contractProvider)

  ContractAbi = ethContract(contractInfo.abi).at(contractInfo.address)

  // console.log('contract', ContractAbi, '\ngetABI', contractInfo.functionHashes, 'contractInfo.address', contractInfo.address)
  return ContractAbi
}

function initMetaMask () {
  // 检查metamask获取当前网络和帐号
  if (typeof window.web3 !== 'undefined') {
    // 检查帐号和网络
    window.web3.version.getNetwork(function (error, response) {
      if (error === null) {
        Store.state.myNetwork = response

        if (typeof window.web3.eth.defaultAccount !== 'undefined') {
          Store.state.myAccount = window.web3.eth.defaultAccount
        }

        console.log('initMetaMask', response, Store.state.myAccount)
      }
    })
  }
}

function hashToByte32 (fileHex) {
  try {
    let res = hexBase.decode(fileHex).toString('hex').replace(/^1220/, '')
    return ('0x' + res).trim()
  } catch (e) {
    Store.state.publicDialog.show = true
    Store.state.publicDialog.context = 'hash to byte error'
    console.log('hash to byte error\n' + e)
    return null
  }
}

function byte32ToHash (fileByte32) {
  try {
    let byteBuff = Buffer.from(fileByte32.replace(/^0x/, '1220'), 'hex')
    let res = hexBase.encode(byteBuff)
    return res
  } catch (e) {
    Store.state.publicDialog.show = true
    Store.state.publicDialog.context = 'byte to hash error'
    console.log('byte to hash error\n' + e)
    return null
  }
}

function strToByte32 (str) {
  try {
    // 字符串长度不大于32
    const byteLength = 36

    if (str.length < byteLength) {
      for (let i = str.length; i < byteLength; i++) {
        str += ' ' // 不够32位的字串补空格
      }
    }

    let res = ethUtil.bufferToHex(ethUtil.setLengthRight(str, byteLength))
    // console.log('tobyte', str, res)
    return res.trim()
  } catch (e) {
    Store.state.publicDialog.show = true
    Store.state.publicDialog.context = 'string to byte error'
    console.log('string to byte error\n' + e)
    return null
  }
}

function byte32ToStr (byte32) {
  try {
    // let res = ethUtil.setLengthRight(byte32, 32).toString()
    let byteBuffer = ethUtil.toBuffer(byte32)
    // let res = byteBuffer.toString('utf-8', 0, byteBuffer.lastIndexOf(0))
    let res = byteBuffer.toString('utf-8', 0)
    // console.log('byte32ToStr', byte32, res, res2, String.fromCharCode.apply(null, res), URL.createObjectURL(byte32))
    return res.toString().trimRight()
  } catch (e) {
    Store.state.publicDialog.show = true
    Store.state.publicDialog.context = 'byte to string error'
    console.log('byte to string error\n' + e)
    return null
  }
}

// 新增用户
function pushAccount (_name, _password, _mail, _presenter) {
  let name = strToByte32(_name)
  if (name === null) {
    return
  }

  let password = strToByte32(_password)
  if (password === null) {
    return
  }

  let mail = strToByte32(_mail)
  if (mail === null) {
    return
  }

  let paramsHex = ethereumjsAbi.rawEncode(['bytes32', 'bytes32', 'bytes32', 'uint'], [name, password, mail, _presenter]).toString('hex')

  let privateKey = Buffer.from(CommunityFundKey, 'hex')
  console.log('pushAccount', _name, _password, _mail, _presenter)
  return new Promise(function (resolve, reject) {
    // 获取随机数web3js.eth.getTransactionCount(addressFrom).toString(16)
    // 已步获得随机数
    web3js.eth.getTransactionCount(CommunityFund, function (error, result) {
      if (!error) {
        let rawTx = {
          nonce: result, // 随机数
          gasPrice: web3js.toHex(Store.state.gasPrice),
          gasLimit: web3js.toHex(Store.state.gasLimit),
          to: contractInfo.address, // 接受方地址或者合约地址
          data: '0x' + '657b1114' + paramsHex// 合约参数
        }
        // 使用私钥对原始的交易信息进行签名，得到签名后的交易数据
        let tx = new Tx(rawTx)
        tx.sign(privateKey)
        let serializedTx = tx.serialize()

        web3js.eth.sendRawTransaction('0x' + serializedTx.toString('hex'), function (err, hash) {
          if (!err) {
            console.log('send hash:', hash)
            let res = JSON.stringify({hash})
            resolve(res) // resolve只能返回单个值， 如果有多个返回值可打包成json再返回
          } else {
            // alert('send error:\n' + err)
            console.log('send error:', err)
            reject(err)
          }
        })
      } else {
        reject(error)
      }
    })
  })
}

// 修改用户信息
function updateAccount (_name, _password, _mail, _validity, _status) {
  let name = strToByte32(_name)
  if (name === null) {
    return
  }

  let password = strToByte32(_password)
  if (password === null) {
    return
  }

  let mail = strToByte32(_mail)
  if (mail === null) {
    return
  }

  console.log('updateAccount', name, password, mail, _validity, _status)
  let paramsHex = ethereumjsAbi.rawEncode(['bytes32', 'bytes32', 'bytes32', 'uint256', 'uint'], [name, password, mail, _validity, _status]).toString('hex')

  let privateKey = Buffer.from(CommunityFundKey, 'hex')

  return new Promise(function (resolve, reject) {
    // 获取随机数web3js.eth.getTransactionCount(addressFrom).toString(16)
    // 已步获得随机数
    web3js.eth.getTransactionCount(CommunityFund, function (error, result) {
      if (!error) {
        // console.log('conaddress', contractInfo.address)
        let rawTx = {
          nonce: result, // 随机数
          gasPrice: web3js.toHex(Store.state.gasPrice),
          gasLimit: web3js.toHex(Store.state.gasLimit),
          to: contractInfo.address, // 接受方地址或者合约地址
          data: '0x' + '1edeec7e' + paramsHex// 合约参数
        }
        // 使用私钥对原始的交易信息进行签名，得到签名后的交易数据
        let tx = new Tx(rawTx)
        tx.sign(privateKey)
        let serializedTx = tx.serialize()

        web3js.eth.sendRawTransaction('0x' + serializedTx.toString('hex'), function (err, hash) {
          if (!err) {
            console.log('updateAccount send hash:', hash)
            let res = JSON.stringify({hash})
            resolve(res) // resolve只能返回单个值， 如果有多个返回值可打包成json再返回
          } else {
            // alert('send error:\n' + err)
            console.log('updateAccount send error:', err)
            reject(err)
          }
        })
      } else {
        reject(error)
      }
    })
  })
}

