// 回答issue
function pushIssueAnswer (_parentID, _name, _context) {
  let name = strToByte32(_name)
  if (name === null) {
    return
  }

  let context = strToByte32(_context)
  if (context === null) {
    return
  }

  console.log('pushIssueAnswer', name, _context)
  let paramsHex = ethereumjsAbi.rawEncode(['uint256, bytes32', 'bytes32'], [_parentID, name, context]).toString('hex')

  let privateKey = Buffer.from(CommunityFundKey, 'hex')

  // 获取随机数web3js.eth.getTransactionCount(addressFrom).toString(16)
  // 已步获得随机数
  web3js.eth.getTransactionCount(CommunityFund, function (error, result) {
    if (!error) {
      // console.log('conaddress', contractInfo.address)
      let rawTx = {
        nonce: result, // 随机数
        gasPrice: web3js.toHex(Store.state.gasPrice),
        gasLimit: web3js.toHex(Store.state.gasLimit),
        to: contractInfo.address, // 接受方地址或者合约地址
        data: '0x' + '07e932bd' + paramsHex// 合约参数
      }
      // 使用私钥对原始的交易信息进行签名，得到签名后的交易数据
      let tx = new Tx(rawTx)
      tx.sign(privateKey)
      let serializedTx = tx.serialize()

      web3js.eth.sendRawTransaction('0x' + serializedTx.toString('hex'), function (err, hash) {
        if (!err) {
          // 如果没有token参数就用系统默认token
          Store.state.publicDialog.show = true
          Store.state.publicDialog.context = 'upload file is pending'
          // alert('upload file is pending \nvideo hash: ' + _videoHash)
          console.log('pushIssueAnswer send hash:', hash)
        } else {
          Store.state.publicDialog.show = true
          Store.state.publicDialog.context = 'upload error'
          // alert('send error:\n' + err)
          console.log('pushIssueAnswer send error:', err)
        }
      })
    } else {
      Store.state.publicDialog.show = true
      Store.state.publicDialog.context = 'getTransactionCount error'
    }
  })
}

