function getVideoInfoByHash (_videoHash) {
  // 直接返回Promise在接受端处理
  // return ContractAbi.getVideoInfoByHash(_videoHashByte)
  let hashByte = hashToByte32(_videoHash)
  return new Promise(function (resolve, reject) {
    ContractAbi.getVideoInfoByHash(hashByte)
      .then(function (response) {
        let videoHash = byte32ToHash(response[0])
        let imgHash = byte32ToHash(response[1])
        let titleStr = byte32ToStr(response[2])
        let tagStr = byte32ToStr(response[3])
        let time = response[4].toString()
        let praise = response[5].toString()
        let status = response[6].toString()
        console.log('getVideoInfoByHash', response, videoHash, imgHash, titleStr, tagStr, praise, time)
        let res = JSON.stringify({videoHash, imgHash, titleStr, tagStr, time, praise, status})
        resolve(res) // resolve只能返回单个值， 如果有多个返回值可打包成json再返回
      })
      .catch(function (error) {
        console.log('error', error)
        reject(error)
      })
  })
}

// 获得字串MD5
function getSecretMd5 (hash, expireTimestamp) {
  // let myKey = 'home' // 此key需和服务器端生成MD5所用吸一致

  // 密钥 组合
  let sercetKey = 'aidworld3541' // key值需前后端保持一值
  let secret = expireTimestamp + sercetKey
  // base64_encode(md5($key, true));  //MD5第二个参数是关键，返回原始二进制数据
  let md5Base64 = MD5js.base64(secret)
  let md5 = JSON.stringify(md5Base64)
  // console.log(md5, md5.substr(3, 1), md5.replace(/=/g, '-'))
  // 替换特殊字符
  for (let i = 0; i < md5.length; i++) {
    switch (md5.substr(i, 1)) {
      case '+':
        md5 = md5.replace('+', '-')
        break
      case '/':
        md5 = md5.replace('/', '_')
        break
    }
  }

  serverHeader()
  getServerTime()
  console.log('getSecretMd5', '\nexpire', expireTimestamp, '\nstr', secret, '\nmd5Baes64', md5Base64, '\nmd5', md5)

  return JSON.parse(md5)
}

function getServerTime () {
  let xmlhttp = null

  if (window.ActiveXObject) {
    xmlhttp = new window.ActiveXObject('Microsoft.XMLHTTP')
  } else {
    xmlhttp = new XMLHttpRequest()
  }

  xmlhttp.open('get', '/', false)
  xmlhttp.setRequestHeader('If-Modified-Since', 'q')
  xmlhttp.send()

  let timestr = xmlhttp.getResponseHeader('Date')

  let date = new Date(timestr)
  let date2 = new Date()

  console.log('getServerTime', date, date2)
}

