<template>
  <v-container style="max-width: 80rem">
    <v-layout justify-center wrap class="floatCenter">
      <v-flex xs12 mb-5>
        <!--context-->
        <!--part1-->
        <v-layout>
          <div style="margin-top: 3rem">
            <div style="text-align: left; margin:0 auto; color: black">
              <!--<div style="font-size: xx-large; margin-top: 1.2rem;color: white">2018.12.06 coming soon</div>-->
              <div style="font-size: xx-large; margin-top: 1.2rem">SEASHELLS TOKEN</div>
              <div style="font-size: x-large; margin-top: 1.2rem; "><span>Buy Price: </span><span>{{this.$store.state.coinMarket.ssCoinContract.buyPrice}} eth</span>
              </div>
              <div style="font-size: x-large; margin-top: 1.2rem; "><span>Total Tokens: </span><span>{{this.$store.state.coinMarket.ssCoinContract.totalToken}} ss</span>
              </div>
            </div>
          </div>
        </v-layout>
        <!--part2-->
        <v-layout justify-center row wrap class="mt-5">
          <v-flex xs12 md4>
            <!--Overall Volume-->
            <div id="div1" class="part2_div_block">
              <div class="block_text" style="font-size: xx-large; margin-top: 1.8rem">Overall Volume</div>
              <div class="block_text">Total Ethereum handled by the smart-exchange</div>
              <div class="block_text" style="margin-top: 0.56rem; margin-bottom: 0.56rem">{{this.$store.state.coinMarket.ssCoinContract.totalETH}} eth
              </div>
              <div class="block_text">10% of all volume the seashells token experiences, is taxxed and split across all
                seashells owners as passive eth income.
              </div>
            </div>
          </v-flex>
          <!--Dividends-->
          <v-flex xs12 md4>
            <div id="div2" class="part2_div_block">
              <div class="block_text" style="font-size: xx-large; margin-top: 1.8rem">Dividends</div>
              <div class="block_text">Total paid out as dividends by the smart-contract</div>
              <div class="block_text" style="margin-top: 0.56rem; margin-bottom: 0.56rem">
                {{this.$store.state.coinMarket.ssCoinContract.totalDividends}} eth
              </div>
              <div class="block_text">The smart contract handles payouts, never humans.</div>
            </div>
          </v-flex>
          <!--All-Time Averages-->
          <v-flex xs12 md4>
            <div id="div3" class="part2_div_block">
              <div class="block_text" style="font-size: xx-large; margin-top: 1.8rem">All-Time Averages</div>
              <div class="block_text">Of The first bonded token</div>
              <div class="block_text"><span>total tokens: </span><span>{{this.$store.state.coinMarket.ssCoinContract.totalToken}} ss</span>
              </div>
              <div class="block_text">
                <span>total dividends: </span><span>{{this.$store.state.coinMarket.ssCoinContract.totalDividends}} eth</span></div>
              <div class="block_text"><span>buy Price: </span><span>{{this.$store.state.coinMarket.ssCoinContract.buyPrice}} eth</span></div>
              <div class="block_text"><span>sell Price: </span><span>{{this.$store.state.coinMarket.ssCoinContract.sellPrice}} eth</span></div>
            </div>
          </v-flex>
        </v-layout>

        <!--part3-->
        <v-layout justify-center row wrap class="mt-5">
          <!--buy-->
          <v-flex xs12 md6>
            <div class="part3_4_div_block">
              <div style="height: 16.25rem">
                <div class="block_text" style="font-size: xx-large; margin-top: 1.8rem">Create Tokens!</div>
                <div class="block_text">Purchase with ETH from your wallet, or reinvest from your Vault to compound your
                  dividend income
                </div>
                <div class="input-box" style="margin-top: 1rem">
                  <input class="input-text" type="text" placeholder=" >0.02 eth" v-model="buyInput"/>
                  <input id="buy_calculate" class="input-submit" type="submit" @click="calculateTokensReceived" value="= ss" />
                </div>
                <div class="block_text" style="margin-top: 2.5rem">There is a 10% Tax, that's how the token works after all.</div>
              </div>
              <div style="text-align: center; margin-top: 0.1rem">
            <span>
            <button class="block_button" @click="Buy">buy</button>
            <button class="block_button" @click="reinvest">reivent</button>
            </span>
              </div>
            </div>
          </v-flex>

          <v-flex xs12 md6>
            <!--sell-->
            <div class="part3_4_div_block ml-2">
              <div style="height: 16.25rem">
                <div class="block_text" style="font-size: xx-large; margin-top: 1.8rem">Sell to the Contract</div>
                <div class="block_text">Sell your seashells instantly at any time for ETH</div>
                <div class="input-box" style="margin-top: 1rem">
                  <input class="input-text" type="text" placeholder="> 1 ss" v-model="sellInput"/>
                  <input id="sell_calculate" class="input-submit" type="submit" @click="calculateEthereumReceived" value="= eth" />
                </div>
                <div class="block_text" style="margin-top: 3rem">Note: ETH will be deposited in your vault. You need to manually withdraw it to
                  your wallet.</div>
                <!--<div class="block_text" style="font-size: medium">Need to pay gas to the Ethereum</div>-->
              </div>
              <div style="text-align: center; margin-top: 0.1rem">
                <button class="block_button" @click="Sell">sell</button>
              </div>
            </div>
          </v-flex>
        </v-layout>

        <!--part4-->
        <v-layout justify-center row wrap class="mt-3 ">
          <v-flex xs12 md6>
            <!--Referral-->
            <div class="part3_4_div_block">
              <div class="block_text" style="font-size: xx-large; margin-top: 1.8rem">Master node:</div>
              <div class="block_text">Owning 100 seashells unlocks the Master node / Referral System</div>
              <div class="block_text">Your referral link and you can share with your friends:</div>
              <div class="block_text" style="height: 5rem; margin-top: 1.2rem; border:0.05rem dashed #FF6B4A;border-radius: 0.1rem">
                <div class="block_text" style="word-break: break-word; word-wrap: normal; font-size: medium">{{this.$store.state.coinMarket.ssCoinAccount.referredLink}}</div>
              </div>
              <div class="block_text" style="font-size: medium">3% of dividends a player uses to buy SS immediately is kicked back to you as a referral reward</div>

            </div>
          </v-flex>
          <v-flex xs12 md6>
            <!--vault-->
            <div class="part3_4_div_block ml-2">
              <div style="height: 16.3rem; background-color: white">
                <div class="block_text" style="font-size: xx-large; margin-top: 1.8rem">Vault: <span
                  style="margin-left: 0.56rem" class="title">{{this.$store.state.myNetwork | networkStatus}}</span>
                </div>
                <div class="block_text"><span>My Account: </span><span style="margin-left: 3px">{{this.$store.state.myAccount}}</span></div>
                <div class="block_text"><span>My Seashells: </span><span style="margin-left: 3px">{{this.$store.state.coinMarket.ssCoinAccount.myTokens}} ss</span>
                </div>
                <div class="block_text"></div>
                <div class="block_text"><span>My Dividends: </span><span style="margin-left: 3px">{{this.$store.state.coinMarket.ssCoinAccount.myDividends}} eth</span>
                </div>
              </div>
              <div style="text-align: center; height: 1.8rem">
                <button class="block_button" @click="withdraw">withdraw</button>
              </div>
            </div>
          </v-flex>
        </v-layout>
        <!--boot-->
        <v-layout justify-center class="mt-5" >

          <div style=" padding-top: 3rem; padding-bottom:3rem; background-color: #6495ED; width: 75rem ">

            <div class="curved_box pt-3 pb-3" style="width: 60rem; color: white; border-color: white; background: transparent">
              <div style="font-size: xx-large; margin-top: 1rem">seashells will be listed on the digital currency exchange in August</div>
              <!--<button @click="goTradingCenter" class="buttonComm" style="font-size: large; margin-top: 3rem">market center</button>-->
            </div>
          </div>
        </v-layout>

        <v-layout justify-end row wrap class="mt-5">
          <span class="mr-5" ><a style="text-decoration:none" href="https://github.com/bliface/video">WhiteBook</a></span>
          <span class="mr-5" ><a style="text-decoration:none" href="https://etherscan.io/address/0x5cf4c6ee0d1f6f9337ab89a79f1885c748b8bee1#contracts">source code</a></span>
        </v-layout>

        <!--<myFoot style="padding-bottom: 5rem; border-top: 1px solid white;"/>-->
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import coinMarket from '../js/coinMarket'
export default {
  name: 'coinMarket',
  data: function () {
    return {
      titleShow: '',
      title: '',
      buyHint: '',
      buyInput: '',
      sellHint: '',
      sellInput: '',
      inputParameter: '',
      myReferredLink: ''
    }
  },
  watch: {
    buyInput: function () {
      this.buyInput = (this.buyInput.match(/^\d*(\.?\d{0,4})/g)[0]) || null
    },
    sellInput: function () {
      if (this.sellInput.substring(0, 1) !== '0') { // 不能以0开头
        this.sellInput = this.sellInput.replace(/[^\d]/g, '') // 只接整形数字
      } else {
        this.sellInput = ''
      }
    }
  },
  filters: {
    // 用于条件显示文本插值
    networkStatus (network) {
      let res = 'invalid MetaMask' // 默认值
      if (network === '0') {
        res = 'Not Login MetaMask'
      }
      if (network === '1') {
        res = 'Network Main'
      }
      if (network === '4') {
        res = 'Network Rinkeby'
      }
      return res
    }
  },
  beforeCreate () {
    document.querySelector('body').setAttribute('style', 'margin: 0; padding:0; background-color:#FF6B4A;')
  },
  // 钩子函数 组件加载完成 数据初始化
  mounted () {
    // 加载推荐者
    if (typeof this.$route.query.token !== 'undefined' && coinMarket.web3js.isAddress(this.$route.query.token)) {
      this.$store.state.coinMarket.ssCoinContract.referredBy = this.$route.query.token
    }

    coinMarket.initContract()
    console.log(this.$options.name, ' log in by:', this.$route.query.token)
  },
  // 方法集合
  methods: {

    Buy () {
      var buyQty = this.buyInput
      coinMarket.buy(buyQty)
    },
    Sell () {
      var sellQty = this.sellInput
      coinMarket.sell(sellQty)
    },

    goOfficialReferral () {
      window.open(this.$store.state.ssCoinContract.officialReferral)
    },
    goTradingCenter () {
      // router.push({name: 'token', query: {token: this.$store.getters.getSysTokenSymbol}})
      let routeUrl = this.$router.resolve({
        path: '/token',
        query: {token: this.$store.state.sysTokenSymbol}
      })
      window.open(routeUrl.href, '_blank')
    },
    /**
       * 取走请求者的所有收益。
       */
    withdraw () {
      coinMarket.withdraw()
    },
    /**
       * 取回请求者拥有的股息。
       * 如果`_includeReferralBonus` 的值为1，那么推荐奖金将被计算在内。
       * 其原因是，在网页的前端，我们希望得到全局汇总。
       * 但在内部计算中，我们希望分开计算。
       */
    // myDividends () {
    //   var res = seashellsSC.seashells.myDividends.call(1)
    //   console.log('myDividends: ' + seashellsSC.web3.fromWei(parseInt(res)))
    // },

    // 将所有的红利再投资为代币。包括股息和分红
    reinvest () {
      coinMarket.reinvest()
    },

    // 计算输入eth的可买入数量
    calculateTokensReceived () {
      let inputValue = this.buyInput
      coinMarket.calculateTokensReceived(inputValue)
    },
    // 动态获取卖出订单价格
    calculateEthereumReceived () {
      let inputValue = Math.round(this.sellInput * 10000) / 10000
      coinMarket.calculateEthereumReceived(inputValue)
    }

  },

  // 钩子函数 组件结束前执行
  beforeDestroy () {
    console.log(this.$options.name, ' log out')
    document.querySelector('body').removeAttribute('style')
  }
}
</script>

<style scoped>
  .background {
    text-align: center;
    margin: 0;
    padding: 0;
    border: 0;
    width: 100%;
    height: 100%;
    background-color:#FF6B4A;
  }
  .guideline{
    float: left;
    margin-left: 0.5rem;
  }
  .part2_div_block{
    text-align:left;
    width: 25rem;
    height: 20rem;
    border:1px solid #6495ED;
    display: block;
    background-color:white;
  }
  .part3_4_div_block {
    text-align:left;
    width: 37rem;
    height: 23rem;
    background-color:white;
    border-radius: 0.3rem;
    box-shadow:
      0 0 1rem #6495ED;
  }
  .block_text {
    font-size: large;
    margin-left: 0.4rem;
    margin-right: 0.4rem;
    margin-top: 0.56rem;
  }
  .block_button {
    width: 12.5rem;
    height: 2.5rem;
    font-size: large;
    background-color: #6495ED;
    color: rgba(250,250,250,1);
    margin:0.56rem auto 0;
    cursor: pointer;
  }
  .input-box {
    width:100%;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    padding: 0.18rem;
  }
  .input-text {
    margin: 0 ;
    padding: 0 ;
    height: 3rem;
    width: 50%;
    font-size: 1.2rem;
    border: 1px solid #D8D8D8;
    border-image: none;
    text-indent: 0.2rem;
    float: left;
  }
  .input-submit {
    margin: 0 ;
    padding: 0 ;
    font-size: 1.2rem;
    background-image: linear-gradient(to bottom, #FBFBFB, #F3F3F3);
    border: 1px solid #D8D8D8;
    cursor: pointer;
    height: 3rem;
    width: 50%;
    -moz-appearance: none;
    text-align: left;
    text-indent: 0.2rem;
    float: left;
  }
  .floatCenter{
    margin: 0 auto;
    display: inline-block;
  }
  .buttonComm {
    color: white;
    width: 12rem;
    height: 3rem;
    font-family: Helvetica;
    background: transparent;
    border:1px solid white;
    border-radius: 0.3rem;
    box-shadow: 0 0 1rem white;
    cursor: pointer;
  }
  .curved_box {
    display: inline-block;
    *display: inline;
    width: 12.5rem;
    height: 15.5rem;
    margin: 1.25rem;
    background-color: #fff;
    border: 0.06rem solid #eee;
    -webkit-box-shadow: 0 0.6rem 2.5rem rgba(0, 0, 0, 0.27), 0 0 5.6rem rgba(0, 0, 0, 0.06) inset;
    -moz-box-shadow: 0 0.6rem 2.5rem rgba(0, 0, 0, 0.27), 0 0 5.6rem rgba(0, 0, 0, 0.06) inset;
    box-shadow: 0 0.6rem 2.5rem rgba(0, 0, 0, 0.27), 0 0 5.6rem rgba(0, 0, 0, 0.06) inset;
    position: relative;
    *zoom: 1;
  }
</style>



