// 检查浏览器
function checkExpress () {
  let ok = 0
  if (navigator.userAgent.indexOf('Firefox') >= 0) {
    ok = 1
  } else if (navigator.userAgent.indexOf('Opera') >= 0) {
    ok = 1
  } else if (navigator.userAgent.indexOf('Chrome') >= 0) {
    ok = 1
  }
  console.log('express:' + ok)
  if (ok === 0) {
    // if (confirm('以太坊metamask不支持当前浏览器及移动设备 \n您可以使用chrome firefox opera brave \n不能正常访问google的用户可使用firefox\n详情可查看https://metamask.io')) {
    //   window.open('https://metamask.io')
    // }
    this.$store.state.publicDialog.show = true
    this.$store.state.publicDialog.context = '以太坊metamask插件不支持当前浏览器 \n您可以使用360, Chrome, Firefox等支持metamask插件的浏览器 \n不能访问google的用户  可使用360或Firefox浏览器'
  }
  return ok
}

