function initContract () {
  if (checkExpress() > 0) {
    if (typeof window.web3 !== 'undefined') {
      window.web3.version.getNetwork((er, netId) => {
        let eth_
        if (typeof netId !== 'undefined') {
          store.state.myNetwork = netId
          // 是否预设网络（开发阶段为测试网 发布阶段为主网）
          if (netId === ssCoin.host) {
            console.log('network is right: ' + store.state.myNetwork)
            // web3js = new Web3(window.web3.currentProvider)
            eth_ = new Eth(window.web3.currentProvider)
          } else {
            console.log('network is wrong: ' + store.state.myNetwork)
            // web3js = new Web3(new Web3.providers.HttpProvider(store.getters.getMainInfura))
            eth_ = new Eth(new Web3.providers.HttpProvider(store.state.mainInfura))
          }
        } else {
          console.log('network is undefined')
          // web3js = new Web3(new Web3.providers.HttpProvider(store.getters.getMainInfura))
          eth_ = new Eth(new Web3.providers.HttpProvider(store.state.mainInfura))
        }

        let ethContract = new EthContract(eth_)
        // coinMarket = ethContract(abi).at(address)

        // coinsHead.initCoinsHead(ethContract)

        coinMarket = ethContract(ssCoin.abi).at(ssCoin.address)

        // store.commit('setContractAddress', ssCoin.address)
        // console.log('contractAddress' + store.getters.getContractAddress)

        initCommMeta()

        setTimeout(initMetadata(), 1000)
      })
    } else { // 当没有运行metamask时 通过加载infura获取只读数据 让程序可以正常显示
      console.log('not metamask & connect: ')
      // web3js = new Web3(new Web3.providers.HttpProvider(store.getters.getMainInfura))
      let eth = new Eth(new Web3.providers.HttpProvider(store.state.mainInfura))

      let ethContract = new EthContract(eth)
      coinMarket = ethContract(ssCoin.abi).at(ssCoin.address)

      // store.commit('setContractAddress', ssCoin.address)
      // console.log('contractAddress' + store.getters.getContractAddress)

      // coinsHead.initCoinsHead(ethContract)

      setTimeout(initMetadata(), 1000)
    }
  }
  return coinMarket
}

