// 获取以太坊公有数据 需在有插件[window.web3]的状态下
function initCommMeta () {
  // 获取当前的gasPrice 如果大于预设值 则取当前动态值
  window.web3.eth.getGasPrice((er, result) => {
    if (result !== null && result > 0) {
      store.state.gasPrice = result
    }
  })
}

// 获取公开函数基本数据
function initMetadata () {
  totalSupply()
  totalEthereumBalance()
  buyPrice()
  sellPrice()

  // 检查网络
  if (typeof window.web3 !== 'undefined') {
    // 检查帐号和网络

    if (window.web3.eth.coinbase !== null && store.state.myNetwork === ssCoin.host) {
      let myAddress = window.web3.eth.coinbase
      if (store.state.myAccount !== myAddress) {
        // 如果更换帐号则先重置帐号数据
        accountClear()
        store.state.myAccount = myAddress
      }
      // 加载帐号数据
      myTokens(myAddress, false)
      myDividends(1, myAddress, false)
    }
  }
  // 每10秒更新基础数据,用归递超时调用来模拟间歇调用，禁用setInterval
  setTimeout(function () {
    if (coinMarket !== null) {
      initMetadata()
    }
  }, 1000 * 12)
}

// 合约以太币总额和收益总量
function totalEthereumBalance () {
  coinMarket.totalEthereumBalance()
    .then((result) => {
      if (result[0].toString() > 0) {
        // Math.round(a * 10000) / 10000 保留小数4位
        let rest = Math.round(web3js.fromWei(result[0].toString()) * 10000) / 10000
        ssCoinStore.ssCoinContract.totalETH = rest
        ssCoinStore.ssCoinContract.totalDividends = Math.round((rest / 10) * 10000) / 10000
      }
    })
}

// 查询已生成token总量
function totalSupply () {
  coinMarket.totalSupply()
    .then((result) => {
      if (result[0].toString() > 0) {
        let rest = Math.round(web3js.fromWei(result[0].toString()) * 10000) / 10000
        ssCoinStore.ssCoinContract.totalToken = rest
      }
    })
}

// 当前地址token数量 参数isEvent是否为事件返回调用 事件返回调用需要添加0值
function myTokens (fromAddress, isEvent) {
  coinMarket.myTokens({from: fromAddress})
    .then((result) => {
      if (result[0].toString() > 0) {
        let rest = Math.round(web3js.fromWei(result[0].toString()) * 100000000) / 100000000
        if (rest > 100) {
          // 用替换原推荐者 生成新推荐者
          // var referredBy_ = store.getters.getReferredBy
          // // 本页地址传值默认为NoReferrer
          // if (referredBy_ === 'NoReferrer' || web3.isAddress(referredBy_)) {
          // // if (referredBy_ !== '') {
          //   var myLink = window.location.href.replace(referredBy_, fromAddress)
          //   store.commit('setMyReferredLink', myLink)
          // }

          // 直接生成参数地址
          ssCoinStore.ssCoinAccount.referredLink = window.location.host + '/coinMarket?token=' + fromAddress
        } else {
          ssCoinStore.ssCoinAccount.referredLink = ''
        }
        ssCoinStore.ssCoinAccount.myTokens = rest
      }
    })
}

// 当前地址红利（参数确定是否计算推荐[referre]收入 1:计算，0：不计算, 参数isEven是否为事件返回调用 事件返回调用需要添加0值）
function myDividends (bool, fromAddress, isEvent) {
  coinMarket.myDividends(bool, {from: fromAddress})
    .then((result) => {
      // console.log('res: ' + result[0].toString() + ' isevent ' + isEvent)
      if (result[0].toString() > 0) {
        let rest = Math.round(web3js.fromWei(result[0].toString()) * 100000000) / 100000000
        ssCoinStore.ssCoinAccount.myDividends = rest
      } else { // 该帐号没有收益
        if (isEvent) {
          ssCoinStore.ssCoinAccount.myDividends = '0'
        }
      }
    })
}

// 检索任意地址的红利
function dividendsOf (address) {
  let res = coinMarket.dividendsOf.call(address)
  return web3js.fromWei(res).toFixed(4).toString()
}

