// 检索任意地址的代币余额
function balanceOf (_pm) {
  let fromAddress = _pm
  coinMarket.balanceOf(fromAddress)
    .then((result) => {
      let rest = '0'
      if (result[0].toString() > 0) {
        rest = Math.round(web3js.fromWei(result[0].toString()) * 10000) / 10000
      }
      console.log('error' + rest)
    })
}

async function buy (_tokenQty) {
  console.log('************* buy **************')

  if (checkExpress() === 0) {
    return
  }
  // metamask安装检查
  if (typeof window.web3 === 'undefined') {
    alert('you need to install and enabled Metamask')
    return
  }
  // 检查网络是否为预设网络
  if (store.state.myNetwork !== ssCoin.host) {
    alert('You need to select the Main Ethereum Network in Metamask') // 开发时可以是测试网 发布时需在全局设置中改为主网
    return
  }

  if (_tokenQty < ssCoinStore.ssCoinContract.buyMinimum) {
    alert('greater than or equal to 0.02 eth')
    return
  }
  // 检查合约地址
  if (!web3js.isAddress(ssCoin.address)) {
    alert('not get contract address')
    return
  }

  await window.web3.eth.getCoinbase((er, myAddress) => {
    if (myAddress == null) {
      alert('You need login Metamask')
    } else {
      // 检查是否为天使阶段 （天使阶段只有天使可以购买）
      // if (store.getters.getOnlyAmbassadors) {
      //   // 检查是否为天使
      //   if (!isAmbassadors(myAddress)) {
      //     alert('2018.12.06 coming soon\nNow is in the testing')
      //     return
      //   }
      // }

      let buyToken = web3js.toWei(_tokenQty, 'ether')

      console.log('\nfrom: ' + myAddress + '\nto: ' + ssCoin.address + '\nvalue: ' + web3js.fromWei(buyToken) + '\nreferredBy: ' + ssCoinStore.ssCoinContract.referredBy)

      // 买入事件
      let watchOnTokenPurchase = coinMarket.onTokenPurchase()
      watchOnTokenPurchase.new()
      watchOnTokenPurchase.watch(function (error, result) {
        if (!error) {
          let res0 = result.args.customerAddress
          let res1 = web3js.fromWei(result.args.incomingEthereum)
          let res2 = web3js.fromWei(result.args.tokensMinted)
          let res3 = result.args.referredBy
          console.log('Purchase result \ncustomerAddress: ' + res0 + ' \nincomingEthereum: ' + res1 + ' \ntokensMinted: ' + res2 + ' \nreferredBy: ' + res3)
          ssCoinStore.ssCoinAccount.myTokens = res2.toString()
          ssCoinStore.ssCoinContract.totalToken = totalEthereumBalance()
        } else {
          console.log('buy event')
          myTokens(myAddress, true)
          myDividends(1, myAddress, true)
        }
        watchOnTokenPurchase.uninstall()
      })

      // 买入
      // coinMarket.buy(referredBy, { from: myAddress, to: store.getters.getContractAddress, value: buyToken, gas: store.getters.getGasLimit, gasPrice: store.getters.getGasPrice },
      coinMarket.buy(ssCoinStore.ssCoinContract.referredBy, { from: myAddress, value: buyToken, gasPrice: store.state.gasPrice },
        function (error, result) {
          if (!error) {
            alert(result + '\nHash has pending please wait')
          } else {
            console.log('buy cancel' + error)
          }
        })
    }
  })
}

async function sell (_sellTokens) {
  console.log('************* sell **************')
  // metamask安装检查
  if (typeof window.web3 === 'undefined') {
    if (confirm('you need to install Metamask' + '\nlink to Metamask website now？')) {
      window.open('https://metamask.io')
    }
    return
  }
  // 检查网络是否为预设网络
  if (store.state.myNetwork !== ssCoin.host) {
    alert('You need to select the Main Ethereum network in Metamask') // 开发时可以是测试网 发布时需在全局设置中改为主网
    return
  }
  // 检查输入下边界
  if (_sellTokens < ssCoinStore.ssCoinContract.sellMinimum) {
    alert('greater than or equal to 1 ' + store.state.sysTokenSymbol)
    return
  }

  // 检查合约地址
  if (!web3js.isAddress(ssCoin.address)) {
    alert('not get contract address')
    return
  }

  // 检查帐号
  await window.web3.eth.getCoinbase((er, myAddress) => {
    if (myAddress == null) {
      alert('You need login Metamask', myAddress)
    } else {
      // 检查上边界
      coinMarket.myTokens({from: myAddress})
        .then((result) => {
          let mybalance = parseInt(web3js.fromWei(result[0].toString()))
          console.log('address: ' + myAddress + ' \nbalance: ' + mybalance + ' \ntokenOfSell: ' + _sellTokens)
          // 帐号余额需大于出售值
          if (mybalance > _sellTokens) {
            // 卖出事件
            let watchOnTokenSell = coinMarket.onTokenSell()
            watchOnTokenSell.new()
            watchOnTokenSell.watch(function (error, result) {
              if (!error) {
                let res0 = result.args.customerAddress
                let res1 = web3js.fromWei(result.args.tokensBurned)
                let res2 = web3js.fromWei(result.args.ethereumEarned)
                console.log('sell result \ncustomerAddress: ' + res0 + ' \ntokensBurned: ' + res1 + ' \nethereumEarned: ' + res2)
              } else {
                console.log('sell event')
                myTokens(myAddress, true)
                myDividends(1, myAddress, true)
              }
              watchOnTokenSell.uninstall()
            })
            // 卖出
            coinMarket.sell(web3js.toWei(_sellTokens), { from: myAddress, gasPrice: store.state.gasPrice },
              function (error, result) {
                if (!error) {
                  alert(result + '\nHash has pending please wait')
                } else {
                  console.log('sell cancel: ' + error)
                }
              })
          } else {
            alert('balance: ' + mybalance + '\nsell: ' + _sellTokens + '\nsell amount out of you balance')
          }
        })
    }
  })
}

// 将所有收益再投资为代币。包括股息和分红
async function reinvest () {
  console.log('************* reinvest **************')
  // metamask安装检查
  if (typeof web3 === 'undefined') {
    if (confirm('you need to install Metamask' + '\nlink to Metamask website now？')) {
      window.open('https://metamask.io')
    }
    return
  }
  // 检查网络是否为预设网络
  if (store.state.myNetwork !== ssCoin.host) {
    alert('You need to select the Main Ethereum network in Metamask') // 开发时可以是测试网 发布时需在全局设置中改为主网
    return
  }
  // 检查合约地址
  if (!web3js.isAddress(ssCoin.address)) {
    alert('not get contract address')
    return
  }
  await window.web3.eth.getCoinbase((er, myAddress) => {
    if (myAddress == null) {
      alert('You need login Metamask')
    } else {
      coinMarket.myDividends(1, {from: myAddress})
        .then((result) => {
          let rest = web3js.fromWei(result[0].toString())
          if (rest < store.getters.getBuyMinimum) {
            alert('Dividends must greater than or equal to 0.02 eth')
          } else {
            // 股息复投事件
            let watchOnReinvestment = coinMarket.onReinvestment()
            watchOnReinvestment.new()
            watchOnReinvestment.watch(function (error, result) {
              if (!error) {
                let res0 = result.args.customerAddress
                let res1 = result.args.ethereumReinvested
                let res2 = result.args.tokensMinted
                console.log('Reinvestment result \ncustomerAddress: ' + res0 + ' \nethereumReinvested: ' + res1 + ' \ntokensMinted: ' + res2)
              } else {
                console.log('reinvest event')
                myTokens(myAddress, true)
                myDividends(1, myAddress, true)
              }
              watchOnReinvestment.uninstall()
            })
            coinMarket.reinvest({ from: myAddress, gasPrice: store.state.gasPrice },
              function (error, result) {
                if (!error) {
                  alert(result + '\nHash has pending please wait')
                } else {
                  console.log('reinvest cancel' + error)
                }
              })
          }
        })
    }
  })
}

// 将所有收益提取为ETH。包括股息和分红
async function withdraw () {
  console.log('************* withdraw **************')
  // metamask安装检查
  if (typeof web3 === 'undefined') {
    if (confirm('you need to install Metamask' + '\nlink to Metamask website now？')) {
      window.open('https://metamask.io')
    }
    return
  }
  // 检查网络是否为预设网络
  if (store.state.myNetwork !== ssCoin.host) {
    alert('You need to select the Main Ethereum network in Metamask') // 开发时可以是测试网 发布时需在全局设置中改为主网
    return
  }
  // 检查合约地址
  if (!web3js.isAddress(ssCoin.address)) {
    alert('not get contract address')
    return
  }
  await window.web3.eth.getCoinbase((er, myAddress) => {
    if (myAddress == null) {
      alert('You need login Metamask')
    } else {
      coinMarket.myDividends(1, {from: myAddress})
        .then((result) => {
          let rest = web3js.fromWei(result[0].toString())
          console.log('withdraw: ' + rest)
          if (rest < ssCoinStore.ssCoinContract.buyMinimum) {
            alert('Dividends must greater than or equal to 0.02 eth')
          } else {
            // 提取股息
            let watchOnWithdraw = coinMarket.onWithdraw()
            watchOnWithdraw.new()
            watchOnWithdraw.watch(function (error, result) {
              if (!error) {
                let res0 = result.args.customerAddress
                let res1 = web3js.fromWei(result.args.ethereumWithdrawn)
                console.log('Withdraw result \ncustomerAddress: ' + res0 + ' \nethereumWithdrawn: ' + res1)
              } else {
                console.log('Withdraw event')
                myTokens(myAddress, true)
                myDividends(1, myAddress, true)
              }
              watchOnWithdraw.uninstall()
            })
            coinMarket.withdraw({ from: myAddress, gasPrice: store.state.gasPrice },
              function (error, result) {
                if (!error) {
                  alert(result + '\nHash has pending please wait')
                } else {
                  console.log('withdraw cancel' + error)
                }
              })
          }
        })
    }
  })
}

function buyPrice () {
  coinMarket.buyPrice()
    .then((result) => {
      if (result[0].toString() > 0) {
        let rest = web3js.fromWei(result[0].toString())
        ssCoinStore.ssCoinContract.buyPrice = rest
      }
    })
}

function sellPrice () {
  coinMarket.sellPrice()
    .then((result) => {
      if (result[0].toString() > 0) {
        let rest = web3js.fromWei(result[0].toString())
        ssCoinStore.ssCoinContract.sellPrice = rest
      }
    })
}

// 动态获取买入订单价格
function calculateTokensReceived (_pm) {
  if (_pm < store.state.coinMarket.ssCoinContract.buyMinimum) {
    alert('greater than or equal to 0.02 eth')
    return
  }
  let pm_ = web3js.toWei(_pm)
  coinMarket.calculateTokensReceived(pm_)
    .then((result) => {
      if (result[0].toString() > 0) {
        let rest = Math.round(web3js.fromWei(result[0].toString()) * 10000) / 10000
        console.log('CalculateTokensReceived:' + rest)
        document.getElementById('buy_calculate').value = '= ' + rest + store.state.sysTokenSymbol
      }
    })
}

// 动态获取卖出订单价格
function calculateEthereumReceived (_pm) {
  // 检查输入下边界
  if (_pm < ssCoinStore.ssCoinContract.sellMinimum) {
    alert('greater than or equal to 1 ' + store.state.sysTokenSymbol)
    return
  }
  // 上边界不能超过总数量
  if (_pm > ssCoinStore.ssCoinContract.totalToken) {
    alert('Is not greater than total tokens ' + ssCoinStore.ssCoinContract.totalToken)
    return
  }
  let pm_ = web3js.toWei(_pm)
  coinMarket.calculateEthereumReceived(pm_)
    .then((result) => {
      if (result[0].toString() > 0) {
        let rest = Math.round(web3js.fromWei(result[0].toString()) * 10000) / 10000
        console.log('calculateEthereumReceived:' + rest)
        document.getElementById('sell_calculate').value = '= ' + rest + ' eth'
      }
    })
}

// 重置当前帐号数据 用于帐号登出或理换时
function accountClear () {
  // console.log('Not account login')
  store.state.myAccount = ''
  ssCoinStore.ssCoinAccount.referredLink = ''
  ssCoinStore.ssCoinAccount.myTokens = 0
  ssCoinStore.ssCoinAccount.myDividends = 0
}

