<template>
    <v-container style="max-width: 37rem">
        <v-layout text-xs-center wrap>
            <v-flex xs12>
                <!--context-->
              <v-layout class="mt-3">
                <h3 class="font-weight-regular">ISSUE 问题反馈</h3>
              </v-layout>

              <v-layout class="mt-2">
              <v-textarea
                outline
                height="12rem"
                clearable
                name="input-7-1"
                label="在此描述您的问题"
                :error-messages="this.contextError"
                v-model="context"
              ></v-textarea>
              </v-layout>
              <v-layout>
                <v-text-field label="name 尊称 optional)" maxLength="50" clearable append-icon="edit" v-model="name"/>
              </v-layout>
              <v-layout>
                <v-text-field label="Email 联系方式（optional)" maxLength="50" clearable append-icon="edit" v-model="contact" />
              </v-layout>

              <v-layout justify-end class="mt-3 mb-5">
                <v-btn :loading="btnLoading" :disabled="btnLoading" color="blue-grey" class="white--text" @click="submitTicket">Submit 提交</v-btn>
              </v-layout>

            </v-flex>
          <!--在屏幕中间显示 共用提示框-->
          <v-dialog v-model="this.$store.state.waitDialog" hide-overlay persistent width="210">
            <v-card color="primary" dark>
              <v-card-text>
                {{this.toast}}
              </v-card-text>
            </v-card>
          </v-dialog>
        </v-layout>
    </v-container>
</template>

<script>
import ethContract from '../js/accountjs'
export default {
  name: 'Ticket',
  data: function () {
    return {
      // 变量
      context: '',
      contextError: '',
      name: '',
      contact: '',
      toast: '',
      btnLoading: false // 控制按钮处于等等状态
    }
  },
  mounted () {
    // 加载时执行
    ethContract.initContract('account')
    this.getIssueCount()
  },
  methods: {
    // 方法合集

    getIssueCount () {
      ethContract.getIssueCount()
        .then(function (resolve) {
          let res = JSON.parse(resolve) // 解析json值
          console.log('getIssueCount', res)
        })
    },
    submitTicket () {
      if (this.context === '') {
        this.contextError = 'context is empty'
        return
      }

      let data = {
        context: this.context,
        name: this.name,
        contact: this.contact,
        userid: '' // 暂为空
      }

      let that = this
      this.btnLoading = true
      ethContract.pushIssueQuestion(data.name, data.context)
        .then(function (resolve) {
          that.btnLoading = false
          that.toast = 'succeed\n感谢你的反馈'
          that.$store.state.waitDialog = true
          setTimeout(function () {
            that.$store.state.waitDialog = false
            that.$router.back()
          }, 1000 * 3)

          console.log('succeed感谢你的反馈', resolve)
        }, function (reject) {
          that.btnLoading = false
          that.toast = '提交失败重试或发邮件反馈'
          that.$store.state.waitDialog = true
          setTimeout(function () {
            that.$store.state.waitDialog = false
          }, 1000 * 3)
          console.log('提交失败重试或发邮件反馈', reject)
        })
    },
    submitTicketAPI () {
      if (this.context === '') {
        this.contextError = 'context is empty'
        return
      }

      let data = {
        context: this.context,
        name: this.name,
        contact: this.contact,
        userid: '' // 暂为空
      }

      let that = this
      this.$http.post('/ticket/', data, {emulateJSON: true})
        .then(function (response) {
          let res = response.data.toString()
          if (res === '1') {
            that.toast = 'succeed\n感谢你的反馈'
            that.$store.state.waitDialog = true
            setTimeout(function () {
              that.$store.state.waitDialog = false
              that.$router.back()
            }, 1000 * 3)
          }
        })
    }
  },
  beforeDestroy () {
    // 组件结束前执行
  }
}
</script>

<style scoped>

</style>



