<template>
  <v-container style="max-width: 80rem">
    <v-layout text-xs-center wrap>
      <v-flex xs12 mb-5>
        <!--context-->
        <v-layout class="ma-1 mt-3" row>
          <v-btn :loading="btnLoading" :disabled="btnLoading" color="blue-grey" class="white--text pt-1" @click="getVideo" >GET Video</v-btn>
          <v-text-field label="video hash" clearable v-model="videoHash"></v-text-field>
          <v-spacer></v-spacer>
           <v-btn flat @click="showVideoSwitch">{{ showVideo | hideShowText }}</v-btn>
        </v-layout>
        <v-layout justify-center  class="green lighten-4 pa-1">
          <video v-show="showVideo" id="player" ref="player" width="100%" height="100%" type="video/mp4" controls/>
        </v-layout>
        <v-layout row justify-end class="mr-2">
          <v-btn flat @click="showVideoSwitch">{{ showVideo | hideShowText }}</v-btn>
        </v-layout>
        <v-layout class="mt-2" row>
          <h3 class="mr-2">Video cover</h3>
          <v-label>[ 640x360 jpg only, max 200k ]</v-label>
        </v-layout>
        <v-flex xs2>
          <v-img id="videoImg" ref="videoImg" contain height="90px" aspect-ratio="1.3" onclick="selectVideoImg.click()">
            <input
              id="selectVideoImg"
              ref="selectVideoImg"
              type="file"
              @change="selectImg($event)"
              style="display: none"
              accept="image/jpeg"
            />
          </v-img>
        </v-flex>
        <v-layout class="mt-3">
          <v-text-field ref="VMVideoTitle" label="TITLE max 32 byte" maxLength="30" clearable hide-details append-icon="edit" v-model="inputTitle"/>
        </v-layout>
        <v-layout class="mt-3">
          <v-text-field label="TAG"  maxLength="30" hide-details clearable append-icon="edit" v-model="inputTag"/>
        </v-layout>
        <!--checkbox array-->
        <v-layout row wrap>
          <v-checkbox v-model="tagArray" label="AI" color="blue-grey" value="AI"></v-checkbox>
          <v-checkbox v-model="tagArray" label="Cartoon" color="blue-grey" value="Cartoon"></v-checkbox>
          <v-checkbox v-model="tagArray" label="First" color="blue-grey" value="First"></v-checkbox>
          <v-checkbox v-model="tagArray" label="Gay" color="blue-grey" value="Gay"></v-checkbox>
          <v-checkbox v-model="tagArray" label="Group" color="blue-grey" value="Group"></v-checkbox>
          <v-checkbox v-model="tagArray" label="Japan" color="blue-grey" value="Japan"></v-checkbox>
          <v-checkbox v-model="tagArray" label="Self" color="blue-grey" value="Self"></v-checkbox>
          <v-checkbox v-model="tagArray" label="SM" color="blue-grey" value="SM"></v-checkbox>
          <v-checkbox v-model="tagArray" label="Student" color="blue-grey" value="Student"></v-checkbox>
          <v-checkbox v-model="tagArray" label="Monster" color="blue-grey" value="Monster"></v-checkbox>
          <v-checkbox v-model="tagArray" label="Teen" color="blue-grey" value="Teen"></v-checkbox>
          <v-checkbox v-model="tagArray" label="Toy" color="blue-grey" value="Toy"></v-checkbox>
        </v-layout>
        <v-layout row wrap>
          <v-checkbox v-model="tagArray" label="Beautiful" color="blue-grey" value="Beautiful"></v-checkbox>
          <v-checkbox v-model="tagArray" label="Baby" color="blue-grey" value="Baby"></v-checkbox>
          <v-checkbox v-model="tagArray" label="Crazy" color="blue-grey" value="Crazy"></v-checkbox>
          <v-checkbox v-model="tagArray" label="SuperStart" color="blue-grey" value="SuperStart"></v-checkbox>
          <v-checkbox v-model="tagArray" label="Tit" color="blue-grey" value="Tit"></v-checkbox>
          <v-checkbox v-model="tagArray" label="Teach" color="blue-grey" value="Teach"></v-checkbox>
        </v-layout>

        <v-layout class="mt-3">
          <v-radio-group v-model="videoStatus" row>
            <v-radio value="1" label="Normal"/>
            <v-radio value="0" label="Disabled"/>
          </v-radio-group>
        </v-layout>
        <v-layout class="mt-3">
          <v-radio-group v-model="inputCost" row>
            <v-radio value="free" label="free"/>
            <v-radio value="vip" label="vip"/>
          </v-radio-group>
        </v-layout>

        <v-container v-if="showTransfer">
          <v-divider class="mt-5"/>
          <v-label>更新智能合约 视频转移到新合约</v-label>
          <v-layout row class="mt-3">
            <v-flex xs5 >
              <v-text-field maxLength="42"
                            label="FromContract"
                            v-model="fromContract"
              ></v-text-field>
            </v-flex>
            <v-flex xs5 class="ml-2" >
              <v-text-field maxLength="42"
                            label="ToContract"
                            v-model="toContract"
              ></v-text-field>
            </v-flex>
            <v-flex xs1 class="ml-2">
              <v-btn round color="blue-grey" class="pl-1 pr-1 ma-0 white--text" @click="transfer" >Transfer
                <v-icon right dark>check_circle</v-icon>
              </v-btn>
            </v-flex>
          </v-layout>
          <v-divider class="mt-3"/>
        </v-container>

        <v-layout justify-center class="mt-5">
          <v-btn round color="blue-grey" class="white--text" @click="update" >UPDATE <v-icon right dark>cloud_upload</v-icon></v-btn>
          <!--<v-btn round color="info" @click="initVideo" >temp</v-btn>-->
          <!--<v-btn round color="info" @click="test" >test</v-btn>-->
          <!--<v-btn round color="info" @click="test4" >test4</v-btn>-->
          <!--<v-btn round color="info" @click="test6" >test6</v-btn>-->
          <!--<v-img ref="testImg">img</v-img>-->
        </v-layout>

        <!--<v-layout justify-center class="mt-5 mb-1" >-->
        <!--<v-label>Upload file need your run IPFS daemon and ethereum MetaMask in your computer</v-label>-->
        <!--</v-layout>-->
        <v-divider class="mt-5"/>
        <v-layout justify-center class="mt-1">
          <v-label>my upload record: {{this.$store.state.myUploadFileList.length}}</v-label>
        </v-layout>

        <!--显示我的上传记录-->
        <v-layout row wrap class="mt-5">
          <v-flex xs12 v-for="(item,index) in this.$store.state.myUploadFileList" :key="index">
            <v-card class="mt-2">
              <v-layout text-xs-left d-flex row wrap class="body-2">
                <v-flex xs2>
                  <!--<v-img src="./static/image/t1.jpg"/>-->
                  <v-img height="8rem" aspect-ratio="1.3" :src="`${item.imgPath}`" :lazy-src="`${item.imgPath}`"/>
                </v-flex>
                <v-flex xs6 class="ml-3">
                  <v-layout class="mt-2">
                    <span>hash: {{item.videoHash}}</span>
                  </v-layout>
                  <v-layout>
                    <span>title: {{item.title}}</span>
                  </v-layout>
                  <v-layout>
                    <span>tags: {{item.tags}}</span>
                  </v-layout>
                  <v-layout>
                    <span>ID: {{item.time}}</span>
                  </v-layout>
                </v-flex>
                <v-flex xs4 class="ml-5">
                  <v-layout class="mt-2 subheading">
                    <!--根据变量值显示不同颜色-->
                    <!--<span :style="'color:'+(item.status=='pending'?'orange':'blue')+';'">status: {{item.status}}</span>-->
                    <span :style="'color:'+(item.status=='pending'?'orange' : '')+';'">status: {{item.status|myVideoShowStatus}}</span>
                  </v-layout>
                  <v-layout>
                    <span>praise {{item.praise}}</span>
                  </v-layout>
                </v-flex>
              </v-layout>
            </v-card>
          </v-flex>
        </v-layout>
      </v-flex>
      <!--在屏幕中间显示 共用等待框-->
      <v-dialog v-model="this.$store.state.waitDialog" hide-overlay persistent width="20rem">
        <v-card color="primary" dark>
          <v-card-text>
            Please stand by
            <v-progress-circular
              indeterminate
              :width="1"
              color="white"
              class="mb-0"
            ></v-progress-circular>
          </v-card-text>
        </v-card>
      </v-dialog>
    </v-layout>
  </v-container>
</template>

<script>
import videojs from 'video.js'
import ethContract from '../js/Ethereum'
import ipfsApi from 'ipfs-api'

export default {
  name: 'Manage',
  data: () => ({
    // 变量
    protocol: 'http', // 当前的网络协议
    inputTitle: '',
    inputTag: '',
    tagArray: [],
    fromContract: '',
    toContract: '',
    inputCost: '', // 视频类型(vip,free)
    videoHash: '',
    barVal: 0,
    num: 1,
    myPlayer: '',
    showTransfer: false, // 智能合约转移功能 默认不可见
    showVideo: true,
    videoInit: {'videoHash': '', 'imgHash': '', 'title': '', 'tag': '', 'time': 0, 'praise': 0, 'status': 1, 'imgPath': ''}, // 记录获取视频时的初始状态， 以判断有没更改过
    videoStatus: 1, // 视频状态 1 为正常，0 为失效
    btnLoading: false // 控制按钮处于等等状态
  }),

  beforeCreate () {
    // 上传界面在https协议下会报错 强制修改为http协议中进行 （需在服务器端改善)
    // window.location.protocol = 'http:'

    // 判断当前的网络协议 默认http
    this.protocol = window.location.protocol.split(':')[0]
  },
  mounted () {
    console.log('log in', this.$options.name, this.protocol)

    this.clearPageMeta()
    // 加载时初始化智能合约
    ethContract.initContract(this.$store.state.sysModel)
    // 加载我的文件上传列表
    this.loadMyFile()
  },
  watch: {
    // 监视字符串数组长度不超32
    tagArray: function () {
      let alength = 0
      for (let i = 0; i < this.tagArray.length; i++) {
        alength += this.tagArray[i].length + 1 // 1为分隔符长度
      }
      // console.log('tagArray length', alength)
      if (alength < 31) {
        this.inputTag = this.tagArray
      }
    },
    inputTitle: function () {
      // 长度不超32
      let maxLength = 32
      let sl = this.$global.getStringByteLength(this.inputTitle)
      if (sl > maxLength) {
        this.inputTitle = this.$global.subStringByte(this.inputTitle, maxLength)
      }
    }
  },
  computed: {
    // getImgByte: function (item) {
    //   return this.videoInfo[item].imgByte
    // }
  },
  filters: {
    // 用于条件显示文本插值

    // 上传列表
    myVideoShowStatus (status) {
      let res = 'normal' // 默认值1时显示
      if (status === 'pending') {
        res = 'pending'
      }
      if (status === 0) {
        res = 'disabled'
      }
      return res
    },

    // 上传视频显示开关文本
    hideShowText (status) {
      let res = 'hide' // 默认值1时显示
      if (!status) {
        res = 'show'
      }
      return res
    }
  },
  methods: {
    // 方法合集
    showVideoSwitch () {
      if (this.showVideo) {
        this.showVideo = false
      } else {
        this.showVideo = true
      }
      window.scrollTo(0, 0) // 滚动到顶部
    },

    // 取择频文件
    getVideo () {
      // let hashByte = ethContract.hashToByte32(this.videoHash)
      if (this.videoHash.length !== 46) {
        this.$store.state.publicDialog.show = true
        this.$store.state.publicDialog.context = 'invalid video hash'
        return
      }

      this.tagArray = []
      let that = this
      this.btnLoading = true
      let videoItem = ethContract.getVideoInfoByHash(this.videoHash)
      videoItem.then(function (resolve) {
        // 成功
        that.btnLoading = false
        let res = JSON.parse(resolve) // 解析json值
        console.log('getVideo resolve', resolve)

        let vmPlayer = that.$refs.player
        let vmImage = that.$refs.videoImg
        vmPlayer.src = that.$store.state.myGatewayHash + res.videoHash
        vmImage.src = that.$store.state.myGatewayHash + res.imgHash

        that.imgHash = res.imgHash
        that.inputTitle = res.titleStr
        that.inputTag = res.tagStr
        that.videoStatus = res.status

        // getVideoInfoByHash 无反回price
        // that.inputCost = 'free'
        // if (typeof res.price !== 'undefined' && res.price.indexOf('vip') > 0) {
        //   that.inputCost = 'vip'
        // }

        // 记录获取视频时的初始状态， 以判断有没更改过
        that.videoInit.status = res.status
        that.videoInit.title = res.titleStr
        that.videoInit.tag = res.tagStr
        that.videoInit.imgHash = res.imgHash
        that.videoInit.imgPath = that.$store.state.myGatewayHash + res.imgHash
      }, function (reject) {
        // 失败
        console.log('getVideo reject', reject)
      })
    },

    initVideo (src) {
      // 初始化视频方法
      let vmPlayer = this.$refs.player
      this.myPlayer = videojs(vmPlayer, {
        // 确定播放器是否具有用户可以与之交互的控件。没有控件，启动视频播放的唯一方法是使用autoplay属性或通过Player API。
        controls: true,
        // 自动播放属性,muted:静音播放
        autoplay: 'muted',
        // 建议浏览器是否应在<video>加载元素后立即开始下载视频数据。
        preload: 'auto'
      })

      // myPlayer.src({src: '../../static/video/test.mp4', type: 'video/mp4'})
      this.myPlayer.src({src: src, type: 'video/mp4'})
      console.log('initVideo', this.myPlayer.src())
    },

    clearPageMeta () {
      // 清理页面数据
      if (this.$store.state.myUploadFileList.length > 0) {
        this.$store.state.myUploadFileList.slice(0, this.$store.state.myUploadFileList.length)
      }
    },
    async test () {
      // ethContract.updateContractPM()
      let time1 = Date.now()
      console.log(this.$global.randomNum(29, 99))

      // 2秒后关闭提示
      await this.$global.sleep(1000 * 2)
      let time2 = Date.now()
      this.$store.state.publicDialog.show = true
      this.$store.state.publicDialog.context = 'go back' + time1 + 'time' + time2
    },

    goTest () {
      console.log('go test: ')
      // this.$router.push({path: '/Detail', query: {hash: videoHash}})
      let routeData = this.$router.resolve({
        path: '/test'
      })
      window.open(routeData.href, '_blank') // 新窗口打开
    },

    // 加载我的上传记录
    loadMyFile () {
      // 检查metamask获取当前网络和帐号
      if (typeof window.web3 !== 'undefined') {
        // 检查帐号和网络
        let that = this
        window.web3.version.getNetwork(function (error, response) {
          if (error === null) {
            console.log('initMetaMask', response, that.$store.state.contractNetworkByDataFile)
            that.$store.state.myNetwork = response
            if (that.$store.state.contractNetworkByDataFile === response) {
              if (window.web3.eth.defaultAccount !== null && typeof window.web3.eth.defaultAccount !== 'undefined') {
                that.$store.state.myAccount = window.web3.eth.defaultAccount
                that.inputMyAccount = that.$store.state.myAccount
                if (that.inputMyAccount !== null) {
                  ethContract.getMyFileList(that.inputMyAccount)
                }
              }
            }
          }
        })
      }
    },

    // 智能合约转移
    transfer () {
      if (this.fromContract.length !== 42) {
        this.$store.state.publicDialog.show = true
        this.$store.state.publicDialog.context = 'invalid FromContract'
        return
      }
      if (this.toContract.length !== 42) {
        this.$store.state.publicDialog.show = true
        this.$store.state.publicDialog.context = 'invalid ToContract'
        return
      }
      if (this.fromContract === this.toContract) {
        this.$store.state.publicDialog.show = true
        this.$store.state.publicDialog.context = 'is same contract'
        return
      }

      // 确认对话框
      let clickBtn = confirm('are you sure transfer contract')
      if (!clickBtn) {
        // 点击取消按钮
        return
      }

      ethContract.transferContract(this.fromContract, this.toContract)
    },

    // 上传修改视频
    update () {
      const videoImg = this.$refs.videoImg
      // console.log('select file', videoImg, selectVideo.files[0])
      // selectVideoImg.value 路径；selectVideoImg.files[0].name文件名

      if (window.location.protocol === 'https:') {
        this.$store.state.publicDialog.show = true
        this.$store.state.publicDialog.context = 'upload video need http Protocol\nplease use http://babyhard.com'
        return
      }

      if (typeof videoImg.src === 'undefined') {
        this.$store.state.publicDialog.show = true
        this.$store.state.publicDialog.context = 'please select video img file'
        return
      }

      if (this.inputTitle === '') {
        this.$store.state.publicDialog.show = true
        this.$store.state.publicDialog.context = 'please input video title'
        return
      }

      // if (this.inputTag === '') {
      //   alert('please input video tag')
      //   return
      // }

      // 判断浏览器是否支持 FileReader
      if (!window.FileReader) {
        this.$store.state.publicDialog.show = true
        this.$store.state.publicDialog.context = '您的设备不支持图片预览功能，如需该功能请升级您的设备！'
        return
      }

      // 检测是否重复提交
      // if (this.$store.state.myUploadFileList.length > 0) {
      //   for (let i = 0; i < this.$store.state.myUploadFileList.length; i++) {
      //     if (this.$store.state.myUploadFileList[i].videoHash === this.videoHash) {
      //       alert('Do not repeat the upload file')
      //       return
      //     }
      //   }
      // }

      // 上传视频
      this.$store.state.waitDialog = true // 显示等待框

      // 如果没有上传帐号，则默认为系统帐号
      let author = this.inputMyAccount
      if (author === '' || typeof author === 'undefined') {
        author = this.$store.state.sysAuthor
      }

      if (this.inputCost === '' || this.inputCost === null) {
        this.inputCost = 'free' // 设置默认值
      }

      // 视频属性写入ETH
      let fileItem = {
        'videoFile': this.videoHash,
        'videoHash': this.videoHash,
        'imgHash': this.imgHash,
        'author': author,
        'category': '1:video', // 按josn格式定义多属性（1为视频大类）
        'title': this.inputTitle,
        'tags': this.inputTag,
        'price': this.inputCost, //  视频价格待定义 以逗号分隔加多个属性(1视频类型（free, vip），2vip时间)
        'memo': '',
        'status': 'pending', // 此状态为前端显示的写入完成状态非后台视频有效状态
        'imgPath': this.$store.state.myGateway + this.imgHash
      }

      // console.log('upload video hash', fileItem.videoHash, '\nimgHash', fileItem.imgHash, '\nauthor', fileItem.author, this.inputTitle, this.inputTag)

      // 优先判断图片， 如果图片改了就全改
      if (videoImg.src !== this.videoInit.imgPath) {
        console.log('video img change', this.videoInit.imgPath)
        // 上传视频图片
        let that = this
        const myIpfs = ipfsApi(that.$store.state.ipfsHost.server, that.$store.state.ipfsHost.port, {protocol: that.protocol})

        let imgBlob = that.base64ToBlob(videoImg.src)
        let videoImgReader = new FileReader()
        videoImgReader.readAsArrayBuffer(imgBlob)
        videoImgReader.onload = function () {
          let videoImgBuffer = Buffer.from(new Uint8Array(videoImgReader.result))
          myIpfs.add(videoImgBuffer)
            .then(function (response) {
              let imgHash = response[0].hash

              // 如果没有上传帐号，则默认为系统帐号
              fileItem.imgHash = imgHash
              // console.log('upload video hash', fileItem.videoHash, '\nimgHash', fileItem.imgHash, '\nauthor', that.inputMyAccount, that.inputTitle, that.inputTag)

              // ethContract.pushVideo(videoHash, videoImgHash, that.$store.state.sysAuthor, _category, that.inputTitle, that.inputTag, _price)
              ethContract.pushVideo(fileItem.videoHash, fileItem.imgHash, fileItem.author, fileItem.category, fileItem.title, fileItem.tags, fileItem.price, fileItem.memo)
              // that.test3(fileItem.videoHash, fileItem.imgHash)
              // that.$store.state.myUploadFileList.unshift(fileItem)
              that.$store.state.waitDialog = false // 消失等待框
            })
            .catch(function (error) {
              console.log('upload video img error', error)
              that.$store.state.waitDialog = false // 消失等待框
            })
        }
      } else {
        // 再有无更改过名称或tag
        if (this.videoInit.title !== this.inputTitle || this.videoInit.tag !== this.inputTag || this.inputCost !== '') {
          console.log('title or tag is change\n', this.inputTitle, this.inputTag)
          // ethContract.pushVideo(videoHash, videoImgHash, that.$store.state.sysAuthor, _category, that.inputTitle, that.inputTag, _price)
          ethContract.pushVideo(fileItem.videoHash, fileItem.imgHash, fileItem.author, fileItem.category, fileItem.title, fileItem.tags, fileItem.price, fileItem.memo)
        }
      }

      // 有无更改过状态 函数带有paypal需经matemask更改
      // if (this.videoInit.status !== this.videoStatus) {
      //   let ddd = parseInt(this.videoStatus)
      //   console.log('video status is change', this.videoInit, ddd)
      //
      //   ethContract.setVideoStatus(fileItem.videoHash, ddd)
      // }
      this.$store.state.myUploadFileList.unshift(fileItem) // 写入本上传记录
      this.$store.state.waitDialog = false // 消失等待框
    },

    // 将base64转换为blob
    base64ToBlob (dataurl) {
      let arr = dataurl.split(',')
      let mime = arr[0].match(/:(.*?);/)[1]
      let bstr = atob(arr[1])
      let n = bstr.length
      let u8arr = new Uint8Array(n)
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n)
      }
      return new Blob([u8arr], {type: mime})
    },

    selectImg (e) {
      let selectFile = e.target

      // 图片不大于200k
      if (this.$global.verificationFileSize(selectFile, 200) === '0') {
        return
      }

      // 判断浏览器是否支持 FileReader
      if (!e || !window.FileReader) {
        this.$store.state.publicDialog.show = true
        this.$store.state.publicDialog.context = '您的设备不支持图片预览功能，如需该功能请升级您的设备！'
        return
      }
      let reader = new FileReader()
      // 设置文件

      // 这里是最关键的一步，转换就在这里
      if (selectFile.files[0]) {
        // console.log('selectFile', selectFile, '\npath', selectFile.value, selectFile.files[0].name, reader.result)

        // 提示图片尺寸
        this.$global.verificationPicDimension(selectFile, 640, 360)

        let viewImg = this.$refs.videoImg

        reader.onload = function (e) {
          let imgData = e.target.result

          viewImg.src = imgData
          // console.log('img', imgData.naturalWidth, imgData.width, viewImg.scrollWidth, viewImg.clientWidth)
        }

        reader.readAsDataURL(selectFile.files[0])
      }
    },

    fileReaderToBuffer (bufferString) {
      let fileSize = bufferString.length
      this.num = 1
      const blockSize = 1 * 1024 * 1024
      let nextSize = Math.min(this.num * blockSize, fileSize)

      let buffer = Buffer.from(bufferString)
      console.log('\nreader selectImg', fileSize, nextSize, buffer)

      let fileBuffer = ''
      // 大于1M，分段上传
      while (fileSize > nextSize) {
        nextSize = Math.min(this.num * blockSize, fileSize)
        const slice = bufferString.slice((this.num - 1) * blockSize, nextSize)
        // const percent = (blockSize * this.num) / fileSize
        // 上传出错则重新上传
        try {
          fileBuffer += Buffer.from(slice)
          // console.log('splitFile2', fileBuffer, nextSize, this.num)
          // response = await this.onUpload2(slice, file, md5code, percent)
        } catch (error) {
          // response = await this.onUpload2(slice, file, md5code, percent)
        }
        this.num++
      }

      return fileBuffer
    }
  },
  beforeDestroy () {
    // 组件结束前执行
  }
}
</script>

