// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import 'babel-polyfill'
import Vue from 'vue'
import App from './App'
import router from './router'
import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.min.css'

import store from './js/store'
import Axios from 'axios'
import global from './js/global'

// const host = process.env.NODE_ENV === 'development' ? 'https://bliface.com/api' : 'https://bliface.com/api' // 根据 process.env.NODE_ENV 的值判断当前是开发还是生产采用不同的配置
const host = process.env.NODE_ENV === 'development' ? 'http://104.192.82.8/api' : 'http://104.192.82.8/api' // 根据 process.env.NODE_ENV 的值判断当前是开发还是生产采用不同的配置

const instance = Axios.create({
  baseURL: host,
  withCredentials: false
})

Vue.use(Vuetify)

Vue.config.productionTip = false
Vue.prototype.$http = instance // use example this.$http.get('/token?token=' + tokenSymbol, {emulateJSON: true})
Vue.prototype.$global = global

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: {App},
  template: '<App/>'
})



