<template>
    <v-container style="max-width: 37rem">
        <v-layout text-xs-center wrap>
            <v-flex xs12 class="ma-2">
                <!--context-->
              <v-layout justify-center class="mt-3">
                <h2 class="font-weight-regular">LOGON</h2>
                <h4 class="font-weight-light"> 登录</h4>
              </v-layout>

              <v-layout>
                <v-text-field label="account 帐号" maxLength="50" :error-messages="this.nicknameError" clearable append-icon="edit" v-model="nickname"/>
              </v-layout>
              <v-layout>
                <v-text-field label="password 密码" maxLength="30" :error-messages="this.passwordError" clearable append-icon="edit" v-model="password" />
              </v-layout>

              <v-btn :loading="btnLoading" :disabled="btnLoading" color="blue-grey" class="white--text mt-3" block @click="login">Login 登录</v-btn>

              <v-layout justify-end class="mt-3">
                <v-btn flat class="blue--text" @click="logon">Logon 注册</v-btn>
              </v-layout>

              <v-layout text-xs-left column class="mt-3 mb-5">
                <h4 class="font-weight-regular">* 近期区块链网络升级, 如果您的帐号失效可重新注册</h4>
              </v-layout>

            </v-flex>

          <!--在屏幕中间显示 共用提示框-->
          <v-dialog v-model="this.$store.state.waitDialog" hide-overlay persistent width="210">
            <v-card color="primary" dark>
              <v-card-text>
                {{this.toast}}
              </v-card-text>
            </v-card>
          </v-dialog>
        </v-layout>
    </v-container>
</template>

<script>
import ethContract from '../../js/accountjs'
export default {
  name: 'Login',
  data: function () {
    return {
      // 变量
      nickname: '',
      nicknameError: '',
      password: '',
      passwordError: '',
      toast: '',
      btnLoading: false // 控制按钮处于等等状态
    }
  },
  mounted () {
    // 加载时执行
    ethContract.initContract('account')
  },
  methods: {
    // 方法合集
    logon () {
      let toPage = '/Logon'
      this.$router.push({path: toPage})
    },

    login () {
      let errorCount = 0
      if (this.nickname === '') {
        this.nicknameError = 'name is empty'
        errorCount = errorCount + 1
      }

      if (this.password === '') {
        this.passwordError = 'password is empty'
        errorCount = errorCount + 1
      }

      if (errorCount === 0) {
        let that = this
        this.btnLoading = true // 控制按钮处于等等状态
        ethContract.getAccountByName(this.nickname)
          .then(function (resolve) {
            // 成功
            that.btnLoading = false
            let res = JSON.parse(resolve) // 解析json值
            console.log('login', res, res.validity, res.validity.indexOf('000'))
            if (res.id !== '0') {
              if (res.password !== that.password) {
                that.toast = 'password invalid'
              } else {
                // 排除默认值2000-01-01 00:00:00
                if (res.validity.indexOf('000') > 0) {
                  res.validity = ''
                }
                let account = { id: res.id, nickname: that.nickname, mail: res.mail, status: res.status, validity: res.validity, presenter: res.presenter, logonTime: res.logonTime }
                localStorage.setItem(that.$store.state.accountKey, JSON.stringify(account))
                // 登录成功，转到帐号管理界面
                that.$router.push({path: '/Account', query: {key: that.nickname}})
                // console.log('login ok', res, localStorage.getItem(accountKey))
              }
            } else {
              that.toast = 'name invalid'
            }

            if (that.toast !== '') {
              that.$store.state.waitDialog = true
              setTimeout(function () {
                that.$store.state.waitDialog = false
              }, 1000 * 5)
            }
          }, function (reject) {
            // 失败
            that.btnLoading = false
            that.toast = '网络错误请重试'
            that.$store.state.waitDialog = true
            setTimeout(function () {
              that.$store.state.waitDialog = false
            }, 1000 * 5)
            console.log('getVideo reject', reject)
          })
      }
    },
    // loginApi () {
    //   let errorCount = 0
    //   if (this.nickname === '') {
    //     this.nicknameError = 'name is empty'
    //     errorCount = errorCount + 1
    //   }
    //
    //   if (this.password === '') {
    //     this.passwordError = 'password is empty'
    //     errorCount = errorCount + 1
    //   }
    //
    //   if (errorCount === 0) {
    //     let that = this
    //     that.$http.get('/user/name/' + this.nickname, {timeout: 3000, emulateJSON: true})
    //       .then(function (response) {
    //         that.toast = ''
    //         let res = response.data
    //         if (res.length > 0) {
    //           if (res[0].password !== that.password) {
    //             that.toast = 'password invalid'
    //           } else {
    //             let account = { id: res[0].id, nickname: res[0].nickname, mail: res[0].mail, description: res[0].description, status: res[0].status, validity: res[0].validity, presenter: res[0].presenter, sysdate: res[0].sysdate }
    //             localStorage.setItem(that.$store.state.accountKey, JSON.stringify(account))
    //             // 登录成功，转到帐号管理界面
    //             that.$router.push({path: '/Account', query: {key: res[0].nickname}})
    //             // console.log('login ok', res, localStorage.getItem(accountKey))
    //           }
    //         } else {
    //           that.toast = 'name invalid'
    //         }
    //
    //         if (that.toast !== '') {
    //           that.$store.state.waitDialog = true
    //           setTimeout(function () {
    //             that.$store.state.waitDialog = false
    //           }, 1000 * 5)
    //         }
    //       })
    //   }
    // }
  },
  beforeDestroy () {
    // 组件结束前执行
  }
}
</script>

<style scoped>

</style>



