          path: toPage
        })

        window.open(routeData.href, '_blank') // 新窗口打开
      } else {
        // 上传界面在https协议下会报错 强制修改为http协议中进行 （需在服务器端改善)
        window.open('http://bliface.com/#/uploadFile', '_blank')
      }

      console.log('go: ', toPage)
    },

    ssCode () {
      let url = 'https://github.com/bliface/video/blob/master/SS%20Source%20Code.sol'
      window.open(url)
    },
    ssWhiteBook () {
      let url = 'https://github.com/bliface/video/blob/master/ss%20whitebook.pdf'
      window.open(url)
    },
    gitHub () {
      let url = 'https://github.com/bliface/video'
      window.open(url)
    },
    // 手机返回健执行
    async goBack () {
      // this.$router.push({'name': 'HelloWorld'})

      // 等待1秒
      // await this.$global.sleep(1000 * 1)
    }
  },
  destroyed () {
    // 关闭手机物理按键监听
    window.removeEventListener('popstate', this.goBack, false) // false阻止默认事件
    window.removeEventListener('scroll', this.scrollPage, false)
  }
}
</script>

<style>
  /*过度阴影*/
  .halfTrans {
    background: linear-gradient(to top, transparent, rgba(0,0,0,0.1));
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    padding-bottom: 0.1rem;
  }
  /*发光字体*/
  .textArea{
    text-shadow:0 0 5px orchid,
    0 0 15px #ffffff,
    0 0 25px #ffffff;
  }
</style>



