      let toPage = '/Logon'
      this.$router.push({path: toPage})
    },
    login () {
      let errorCount = 0
      if (this.loginAccount.name === '') {
        this.loginAccount.nameError = 'name is empty'
        errorCount = errorCount + 1
      }

      if (this.loginAccount.password === '') {
        this.loginAccount.passwordError = 'password is empty'
        errorCount = errorCount + 1
      }

      if (errorCount === 0) {
        let that = this
        this.btnLoading = true
        AccountContract.getAccountByName(this.loginAccount.name)
          .then(function (resolve) {
            // 成功
            that.btnLoading = false
            let res = JSON.parse(resolve) // 解析json值
            console.log('login', res.password, that.loginAccount.name)
            if (res.id !== '0') {
              if (res.password !== that.loginAccount.password) {
                that.toast = 'password invalid'
              } else {
                // 排除默认值2000-01-01 00:00:00
                if (res.validity.indexOf('000') > 0) {
                  res.validity = ''
                }
                let account = { id: res.id, nickname: that.loginAccount.name, mail: res.mail, status: res.status, validity: res.validity, presenter: res.presenter, logonTime: res.logonTime }
                localStorage.setItem(that.$store.state.accountKey, JSON.stringify(account))
                // 登录成功
                that.toast = 'login successfully'

                // 登录成功关闭登录框
                that.loginDialogShow = false
                // 修改登录状态
                that.$store.state.loginStatus = true
                // that.$router.push({path: '/Account', query: {key: res[0].nickname}})

                // console.log('login ok', res, localStorage.getItem(accountKey))
              }
            } else {
              that.toast = 'name invalid'
            }

            if (that.toast !== '') {
              that.$store.state.waitDialog = true
              setTimeout(function () {
                that.$store.state.waitDialog = false
              }, 1000 * 3)
            }
          }, function (reject) {
            // 失败
            that.btnLoading = false
            that.toast = '网络错误请重试或反馈该问题'
            that.$store.state.waitDialog = true
            setTimeout(function () {
              that.$store.state.waitDialog = false
            }, 1000 * 3)
            console.log('login error reject', reject)
          })
      }
    },
    // loginApi () {
    //   let errorCount = 0
    //   if (this.loginAccount.name === '') {
    //     this.loginAccount.nameError = 'name is empty'
    //     errorCount = errorCount + 1
    //   }
    //
    //   if (this.loginAccount.password === '') {
    //     this.loginAccount.passwordError = 'password is empty'
    //     errorCount = errorCount + 1
    //   }
    //
    //   if (errorCount === 0) {
    //     let that = this
    //     that.$http.get('/user/name/' + this.loginAccount.name, {timeout: 3000, emulateJSON: true})
    //       .then(function (response) {
    //         that.toast = ''
    //         let res = response.data
    //         if (res.length > 0) {
    //           if (res[0].password !== that.loginAccount.password) {
    //             that.toast = 'password invalid'
    //           } else {
    //             let account = { id: res[0].id, nickname: res[0].nickname, mail: res[0].mail, status: res[0].status, validity: res[0].validity, presenter: res[0].presenter, sysdate: res[0].sysdate }
    //             localStorage.setItem(that.$store.state.accountKey, JSON.stringify(account))
    //             // 登录成功
    //             that.toast = 'login successfully'
    //
    //             // 登录成功关闭登录框
    //             that.loginDialogShow = false
    //             // 修改登录状态
    //             that.$store.state.loginStatus = true
    //             // that.$router.push({path: '/Account', query: {key: res[0].nickname}})
    //
    //             // console.log('login ok', res, localStorage.getItem(accountKey))
    //           }
    //         } else {
    //           that.toast = 'name invalid'
