      this.$store.state.publicDialog.show = true
      this.$store.state.publicDialog.context = 'go back' + time1 + 'time' + time2
    },

    // vue video player
    onPlayerPlay (player) {
      // 播放时判断登录状态
      this.account = JSON.parse(localStorage.getItem(this.$store.state.accountKey))
      if (this.account === '' || this.account === null) {
        this.loginDialogShow = true
        player.pause()
      } else {
        // 取视频类型
        let price1 = this.playerVideoInfo.price

        // 判断视频是否是VIP
        if (price1.indexOf('vip') > 0) {
          if (this.account.validity === '' || this.account.validity === null) {
            // 如果没有VIP时间
            this.$store.state.publicDialog.show = true
            this.$store.state.publicDialog.context = '您的帐号没有可用的VIP时间'
            player.pause()
          } else {
            // 时间过期
            let that = this
            AccountContract.getTime()
              .then(function (resolve) {
                // 成功
                let res = JSON.parse(resolve) // 解析json值
                let myTime = new Date(that.account.validity)
                let serverTime = new Date(res.time)
                // 如果VIP时间过期
                if (myTime < serverTime) {
                  // 如果没有token参数就用系统默认token
                  that.$store.state.publicDialog.show = true
                  that.$store.state.publicDialog.context = '您的帐号没有可用的VIP时间'
                  player.pause()
                }
              })
          }
        }
      }
    },
    onPlayerPause (player) {
      // console.log('player pause!', player)
    },
    onPlayerEnded (player) {
      // console.log('player ended!', player)
    },
    onPlayerLoadeddata (player) {
      console.log('player Loadeddata!', player, this.playerOptions)
    },
    onPlayerWaiting (player) {
      console.log('player Waiting!', player)
    },
    onPlayerPlaying (player) {
      // console.log('player Playing!', player)
    },
    onPlayerTimeupdate (player) {
      // console.log('player Timeupdate!', player.currentTime())
    },
    onPlayerCanplay (player) {
      // console.log('player Canplay!', player)
    },
    onPlayerCanplaythrough (player) {
      // console.log('player Canplaythrough!', player)
    },
    // or listen state event
    playerStateChanged (playerCurrentState) {
      // console.log('player current update state', playerCurrentState)
    },
    // player is ready
    playerReadied (player) {
      // seek to 10s

      // console.log('example player 1 readied', player)
      player.currentTime(10)
      // console.log('example 01: the player is readied', player)
    }
  },
  // 组件结束执行
  destroyed () {
    console.log('Detail log out')
    // 关闭手机物理按键监听
    // window.removeEventListener('popstate', this.goBack, false) // false阻止默认事件

    // 移除横屏监听
    // window.removeEventListener('resize', this.renderResize, false)
  }
}
</script>

