<template>
    <v-container style="max-width: 37rem">
        <v-layout text-xs-center wrap>
            <v-flex xs12>
                <!--context-->
              <v-layout justify-center class="mt-3">
                <h2 class="font-weight-regular">UPDATE</h2>
                <h4 class="font-weight-light"> 修改</h4>
              </v-layout>

              <v-layout class="ma-2">
                <v-text-field label="account 帐号" maxLength="50" readonly v-model="nickname"/>
              </v-layout>
              <v-layout class="ma-2">
                <v-text-field label="password 密码" maxLength="30" :error-messages="this.passwordError" clearable append-icon="edit" v-model="password" />
              </v-layout>
              <v-layout class="ma-2">
                <v-text-field label="Confirm Password 确认密码" maxLength="30" :error-messages="this.confirmPasswordError" clearable hide-details append-icon="edit" v-model="confirmPassword"/>
              </v-layout>
              <v-layout class="ma-2">
                <v-text-field label="Email 邮箱 (optional)" maxLength="50" clearable hide-details append-icon="edit" v-model="Email"/>
              </v-layout>

              <v-layout class="ml-2 mb-3">
                <h3 v-show="this.errorHint!=''" class="red--text font-weight-light mt-2">{{this.errorHint}}</h3>
              </v-layout>
              <v-btn :loading="btnLoading" :disabled="btnLoading" color="blue-grey" class="white--text mt-2 mb-5" style="min-width:23rem" @click="update">update 修改</v-btn>

            </v-flex>

          <!--在屏幕中间显示 共用提示框-->
          <v-dialog v-model="this.$store.state.waitDialog" hide-overlay persistent width="210">
            <v-card color="primary" dark>
              <v-card-text>
                {{this.toast}}
              </v-card-text>
            </v-card>
          </v-dialog>

        </v-layout>
    </v-container>
</template>

<script>
import ethContract from '../../js/accountjs'
export default {
  name: 'update',
  data: function () {
    return {
      // 变量
      id: 0,
      nickname: '',
      nicknameError: '',
      password: '',
      passwordError: '',
      confirmPassword: '',
      confirmPasswordError: '',
      Email: '',
      validityMeta: 0,
      presenter: '',
      presenterName: '',
      presenterError: '',
      errorHint: '',
      toast: '',
      btnLoading: false // 控制按钮处于等等状态
    }
  },
  mounted () {
    // 加载时执行

    // 加载时 接收主参数 初始化视频信息
    let queryKey = this.$route.query.key
    ethContract.initContract('account')
    this.getAccount(queryKey)
  },
  methods: {
    // 方法合集
    login () {
      let toPage = '/Login'
      this.$router.push({path: toPage})
    },
    getAccount (queryKey) {
      let that = this
      ethContract.getAccountByid(queryKey)
        .then(function (resolve) {
          // 成功
          let res = JSON.parse(resolve) // 解析json值

          console.log('account', res.validity, res.validityMeta)
          if (res.name !== '') {
            that.nickname = res.name
            that.password = res.password
            that.confirmPassword = res.password
            that.validityMeta = res.validityMeta
            that.Email = res.mail
          } else {
            that.$store.state.publicDialog.show = true
            that.$store.state.publicDialog.context = '未能找到帐号信息\n可将该问题反馈给客服'
          }
        })
    },
    update () {
      // validity购买vip为小时数，默认25 (24 + 1)
      if (this.password !== '') {
        if (this.confirmPassword !== this.password) {
          this.confirmPasswordError = 'confirmPassword is empty'
          this.errorHint = 'confirmPassword unequal to password'
          return
        }
      }

      let data = {}
      if (this.confirmPassword !== '') {
        data.password = this.password
      }
      if (this.mail !== '') {
        data.mail = this.Email
      }
      data.nickname = this.nickname
      data.validityMeta = this.validityMeta // 取原有值
      data.status = 1 // 默认为1正常 0失效
      let that = this
      this.btnLoading = true // 控制按钮处于等等状态
      ethContract.updateAccount(data.nickname, data.password, data.mail, data.validityMeta, data.status)
        .then(function (resolve) {
          that.btnLoading = false
          that.toast = 'succeed\n修改成功 请登录'
          that.$store.state.waitDialog = true
          setTimeout(function () {
            that.$store.state.waitDialog = false
            localStorage.removeItem(that.$store.state.accountKey)
            that.$store.state.accountShow = 'login'
            that.login()
          }, 1000 * 3)
          console.log('修改成功', resolve)
        }, function (reject) {
          that.btnLoading = false
          that.toast = 'invalid\n无效'
          that.$store.state.waitDialog = true
          setTimeout(function () {
            that.$store.state.waitDialog = false
          }, 1000 * 3)
          console.log('修改失败', reject)
        })
    },

    updateAPI () {
      // validity购买vip为小时数，默认25 (24 + 1)
      if (this.password !== '') {
        if (this.confirmPassword !== this.password) {
          this.confirmPasswordError = 'confirmPassword is empty'
          this.errorHint = 'confirmPassword unequal to password'
          return
        }
      }

      let data = {}
      if (this.confirmPassword !== '') {
        data.password = this.password
      }
      if (this.mail !== '') {
        data.mail = this.Email
      }
      if (this.description !== '') {
        data.description = this.description
      }
      let that = this
      this.$http.put('/user/' + this.id, data, {emulateJSON: true})
        .then(function (response) {
          let res = response.data.toString()
          if (res === '1') {
            that.toast = 'succeed\n修改成功 请登录'
            that.$store.state.waitDialog = true
            setTimeout(function () {
              that.$store.state.waitDialog = false
              localStorage.removeItem(that.$store.state.accountKey)
              that.$store.state.accountShow = 'login'
              that.login()
            }, 1000 * 3)
          } else {
            that.toast = 'invalid\n无效'
            that.$store.state.waitDialog = true
            setTimeout(function () {
              that.$store.state.waitDialog = false
            }, 1000 * 3)
          }
        })
    },
    getAccountAPI (queryKey) {
      let that = this
      this.$http.get('/user/id/' + queryKey, {timeout: 3000, emulateJSON: true})
        .then(function (response) {
          let res = response.data
          if (res.length > 0) {
            that.id = res[0].id
            that.nickname = res[0].nickname
            that.password = res[0].password
            that.confirmPassword = res[0].password
            that.Email = res[0].mail
            that.description = res[0].description
          } else {
            this.$store.state.publicDialog.show = true
            this.$store.state.publicDialog.context = '未能找到帐号信息\n可将该问题反馈给客服'
          }
        })
    }
  },
  beforeDestroy () {
    // 组件结束前执行
  }
}
</script>

<style scoped>

</style>



