<template>
    <v-container style="max-width: 37rem">
        <v-layout text-xs-center wrap>
            <v-flex xs12 class="ma-2">
                <!--context-->

              <v-layout justify-center class="mt-3">
                <h2 class="font-weight-regular">LOGON</h2>
                <h4 class="font-weight-light"> 匿名注册</h4>
              </v-layout>

              <v-layout>
                <v-text-field label="account 帐号" maxLength="15" :error-messages="this.nicknameError" clearable v-model="nickname"/>
                <v-icon @click="CheckName" class="cursor: pointer" medium>refresh</v-icon>
              </v-layout>
              <v-layout>
                <v-text-field label="password 密码" maxLength="15" :error-messages="this.passwordError" clearable append-icon="edit" v-model="password" />
              </v-layout>
              <v-layout>
                <v-text-field label="Confirm Password 确认密码" maxLength="15" :error-messages="this.confirmPasswordError" clearable hide-details append-icon="edit" v-model="confirmPassword"/>
              </v-layout>
              <v-layout>
                <v-text-field label="Email 邮箱 (optional)" maxLength="30" clearable hide-details append-icon="edit" v-model="Email"/>
              </v-layout>
              <v-layout>
                <v-text-field label="Invitation code 邀请码 (optional)" maxLength="15" :error-messages="this.presenterError" v-model="presenter"/>
                <v-icon @click="CheckPresenter" class="cursor: pointer" medium>refresh</v-icon>
              </v-layout>
              <v-layout class="ml-2">
                <h3 class="blue--text font-weight-regular" v-show="this.presenterName!=''">{{this.presenterName}}</h3>
              </v-layout>

              <v-layout class="ml-2 mb-3">
                <h3 v-show="this.errorHint!=''" class="red--text font-weight-light mt-2">{{this.errorHint}}</h3>
              </v-layout>
              <v-btn :loading="btnLoading" :disabled="btnLoading" color="blue-grey" class="white--text mt-2" block @click="logon">Logon 注册</v-btn>

              <!--<v-btn :loading="btnLoading" :disabled="btnLoading" color="blue-grey" class="white&#45;&#45;text" block @click="transData2Block">push user</v-btn>-->
              <!--<v-btn :loading="btnLoading" :disabled="btnLoading" color="blue-grey" class="white&#45;&#45;text" block @click="getAccountCount">gotaccount</v-btn>-->
              <!--<v-btn :loading="btnLoading" :disabled="btnLoading" color="blue-grey" class="white&#45;&#45;text" block @click="add">add user</v-btn>-->

              <v-layout justify-end class="mt-3">
                <v-btn flat class="blue--text" @click="login">Login 登录</v-btn>
              </v-layout>

              <v-layout text-xs-left column class="mt-3 mb-5">
                <h4 class="font-weight-regular">* 邀请码为选填 如果有邀请码你和邀请人都将获得24小时VIP资格，以注册时算起，多请多送</h4>
                <h4 class="font-weight-regular">* 您注册后也将拥有自己的邀请码</h4>
                <h4 class="font-weight-regular">* 区块生成需要时间 注册 发贴或返馈等与区块链交互需等待约1分钟</h4>
                <h4 class="font-weight-regular">* 官网bliface.com</h4>
              </v-layout>
            </v-flex>

          <!--在屏幕中间显示 共用提示框-->
          <v-dialog v-model="this.$store.state.waitDialog" hide-overlay persistent width="210">
            <v-card color="primary" dark>
              <v-card-text>
                {{this.toast}}
              </v-card-text>
            </v-card>
          </v-dialog>
        </v-layout>
    </v-container>
</template>

<script>
import ethContract from '../../js/accountjs'
export default {
  name: 'Logon',
  data: function () {
    return {
      // 变量
      nickname: '',
      nicknameError: '',
      password: '',
      passwordError: '',
      confirmPassword: '',
      confirmPasswordError: '',
      Email: '',
      presenter: '',
      presenterName: '',
      presenterError: '',
      errorHint: '',
      toast: '',
      uersList: [{id: '', name: '', password: '', mail: '', presenter: ''}],
      addIndex: 1,
      btnLoading: false // 控制按钮处于等等状态
    }
  },
  mounted () {
    // 加载时执行
    this.$store.state.waitDialog = false // 初始关闭提示
    ethContract.initContract('account')
  },
  watch: {
    // presenter: function () {
    //   if (this.presenter.substring(0, 1) !== '0') { // 不能以0开头
    //     this.presenter = this.presenter.replace(/[^\d]/g, '') // 只接整形数字
    //   } else {
    //     this.presenter = ''
    //   }
    // }
  },
  methods: {
    // 方法合集
    login () {
      let toPage = '/Login'
      this.$router.push({path: toPage})
    },
    CheckName () {
      let that = this
      if (this.nickname === '') {
        this.$store.state.waitDialog = true
        this.toast = 'nickname is empty'
        setTimeout(function () {
          that.$store.state.waitDialog = false
        }, 1000 * 3)
        return
      }
      ethContract.getAccountByName(this.nickname)
        .then(function (resolve) {
          let res = JSON.parse(resolve) // 解析json值
          if (res.id !== '0') {
            // 帐号已存在
            that.toast = 'name exist\n 帐号已存在'
            that.$store.state.waitDialog = true
            setTimeout(function () {
              that.$store.state.waitDialog = false
            }, 1000 * 3)
            console.log('帐号已存在 ', res)
          } else {
            // 帐号已存在
            that.toast = 'name OK\n 该名称可以注册'
            that.$store.state.waitDialog = true
            setTimeout(function () {
              that.$store.state.waitDialog = false
            }, 1000 * 3)
            console.log('该名称可以注册 ', res)
          }
        }, function (reject) {
          // 失败
          console.log('getAccountByName reject error', reject)
        })
    },
    CheckPresenter () {
      let that = this
      this.presenterName = ''
      if (this.presenter === '') {
        this.$store.state.waitDialog = true
        this.toast = 'Invitation code is empty'
        setTimeout(function () {
          that.$store.state.waitDialog = false
        }, 1000 * 3)
        return
      }
      ethContract.getAccountByid(this.presenter)
        .then(function (resolve) {
          let res = JSON.parse(resolve) // 解析json值
          if (res.status !== '0') {
            that.presenterName = 'inviter:' + res.name
            console.log('inviter', res)
          } else {
            // 帐号已存在
            that.presenterName = 'Not find inviter'
            console.log('inviter invalid ', res)
          }
        }, function (reject) {
          // 失败
          console.log('CheckPresenter reject error', reject)
        })
    },
    // 将数据库用户导入区块链
    transData2Block () {
      console.log('transData2Block')
      let that = this
      this.$http.get('/User', {emulateJSON: true})
        .then(function (response) {
          that.uersList.splice(0, that.uersList.length)
          let res = response.data
          console.log('transData2Block 2', res.length)
          if (res.length > 0) {
            for (let i = res.length - 1; i >= 0; i--) {
              let presenter = {}
              presenter.id = res[i].id
              presenter.name = res[i].nickname
              presenter.password = res[i].password
              presenter.mail = res[i].mail
              presenter.presenter = res[i].presenter

              that.uersList.push(presenter)
            }
          }
          console.log(that.uersList)
        })
    },
    getAccountCount () {
      ethContract.getAccountCount()
        .then(function (resolve) {
          // 成功
          let res = JSON.parse(resolve) // 解析json值
          console.log('usercount', res)
        })
    },
    add () {
      let that = this
      if (this.uersList[this.addIndex].id !== '') {
        ethContract.getAccountByName(this.uersList[this.addIndex].name)
          .then(function (resolve) {
            let res = JSON.parse(resolve) // 解析json值
            if (res.id !== '0') {
              console.log('getAccountByName 帐号已存在', res.id, that.uersList[that.addIndex].name)
              that.addIndex = that.addIndex + 1
            } else {
              // console.log('OK2', that.uersList.length, that.uersList[i].id, that.uersList[i].name, that.uersList[i].password, that.uersList[i].mail, that.uersList[i].presenter)
              ethContract.pushAccount(that.uersList[that.addIndex].name.toString(), that.uersList[that.addIndex].password.toString(), that.uersList[that.addIndex].mail.toString(), that.uersList[that.addIndex].presenter.toString())
                .then(function (resolve) {
                  console.log('OK', resolve)
                  that.addIndex = that.addIndex + 1
                  console.log('next', that.addIndex, that.uersList[that.addIndex].name.toString(), that.uersList[that.addIndex].password.toString(), that.uersList[that.addIndex].mail.toString(), that.uersList[that.addIndex].presenter.toString())
                }, function (reject) {
                  that.addIndex = that.addIndex + 1
                  console.log('注册失败', reject)
                })
            }
          })
      }
    },
    logon () {
      let errorCount = 0
      this.errorHint = ''
      this.presenterName = ''
      if (this.nickname === '') {
        this.nicknameError = 'name is empty'
        this.errorHint = this.errorHint + 'name is empty '
        errorCount = errorCount + 1
      }

      if (this.password === '') {
        this.passwordError = 'password is empty'
        this.errorHint = this.errorHint + 'password is empty '
        errorCount = errorCount + 1
      }

      if (this.confirmPassword === '') {
        this.confirmPasswordError = 'confirmPassword is empty'
        this.errorHint = this.errorHint + 'confirmPassword is empty '
        errorCount = errorCount + 1
      } else {
        if (this.confirmPassword !== this.password) {
          this.confirmPasswordError = 'confirmPassword unequal to password'
          this.errorHint = this.errorHint + 'confirmPassword invalid'
          errorCount = errorCount + 1
        }
      }

      if (errorCount === 0) {
        let data = {
          nickname: this.nickname,
          password: this.password,
          mail: this.Email,
          presenter: this.presenter
        }
        let that = this
        this.btnLoading = true // 按钮等待
        ethContract.getAccountByName(data.nickname)
          .then(function (resolve) {
            // 成功
            let res = JSON.parse(resolve) // 解析json值
            if (res.id !== '0') {
              // 帐号已存在
              that.btnLoading = false
              that.toast = 'name exist\n 帐号已存在'
              that.$store.state.waitDialog = true
              setTimeout(function () {
                that.$store.state.waitDialog = false
              }, 1000 * 3)
              console.log('getAccountByName 帐号已存在', res)
            } else {
              ethContract.pushAccount(data.nickname, data.password, data.mail, data.presenter)
                .then(function (resolve) {
                  that.toast = 'succeed\n注册成功 请登录'
                  that.$store.state.waitDialog = true
                  that.btnLoading = false
                  setTimeout(function () {
                    that.$store.state.waitDialog = false
                    that.login()
                  }, 1000 * 3)
                  console.log('注册成功', resolve)
                }, function (reject) {
                  that.toast = '注册失败请重试或反馈该问题'
                  that.$store.state.waitDialog = true
                  that.btnLoading = false
                  setTimeout(function () {
                    that.$store.state.waitDialog = false
                  }, 1000 * 3)
                  console.log('注册失败', reject)
                })
            }
          }, function (reject) {
            // 失败
            that.toast = '注册失败请重试或反馈该问题'
            that.$store.state.waitDialog = true
            that.btnLoading = false
            setTimeout(function () {
              that.$store.state.waitDialog = false
            }, 1000 * 3)
            console.log('getAccountByName reject', reject)
          })
      }
    },

    // api 连接数据库 已改用区块链
    logonAPI () {
      let errorCount = 0
      this.errorHint = ''
      this.presenterName = ''
      if (this.nickname === '') {
        this.nicknameError = 'name is empty'
        this.errorHint = this.errorHint + 'name is empty '
        errorCount = errorCount + 1
      }

      if (this.password === '') {
        this.passwordError = 'password is empty'
        this.errorHint = this.errorHint + 'password is empty '
        errorCount = errorCount + 1
      }

      if (this.confirmPassword === '') {
        this.confirmPasswordError = 'confirmPassword is empty'
        this.errorHint = this.errorHint + 'confirmPassword is empty '
        errorCount = errorCount + 1
      } else {
        if (this.confirmPassword !== this.password) {
          this.confirmPasswordError = 'confirmPassword unequal to password'
          this.errorHint = this.errorHint + 'confirmPassword invalid'
          errorCount = errorCount + 1
        }
      }

      if (errorCount === 0) {
        let data = {
          nickname: this.nickname,
          password: this.password,
          mail: this.Email,
          description: this.description,
          presenter: this.presenter
        }
        let that = this
        this.$http.get('/user/name/' + this.nickname, {emulateJSON: true})
          .then(function (response) {
            let res = response.data
            if (res.length > 0) {
              // 帐号已存在
              that.toast = 'name exist\n 帐号已存在'
              that.$store.state.waitDialog = true
              setTimeout(function () {
                that.$store.state.waitDialog = false
              }, 1000 * 3)
            } else {
              that.$http.post('/User/', data, {emulateJSON: true})
                .then(function (response) {
                  let res = response.data.toString()
                  if (res === '1') {
                    that.toast = 'succeed\n注册成功 请登录'
                    that.$store.state.waitDialog = true
                    setTimeout(function () {
                      that.$store.state.waitDialog = false
                      that.login()
                    }, 1000 * 3)
                  }
                })
            }
          })
      }
    },

    // 检查名称是否已存在 api方式
    CheckNameAPI () {
      let that = this
      if (this.nickname === '') {
        this.$store.state.waitDialog = true
        this.toast = 'nickname is empty'
        setTimeout(function () {
          that.$store.state.waitDialog = false
        }, 1000 * 3)
        return
      }
      this.$http.get('/user/name/' + this.nickname, {timeout: 3000, emulateJSON: true})
        .then(function (response) {
          let res = response.data
          console.log('CheckName', response)
          if (res.length > 0) {
            that.toast = 'nickname already exist'
          } else {
            that.toast = 'nickname is OK'
          }
          that.$store.state.waitDialog = true
          setTimeout(function () {
            that.$store.state.waitDialog = false
          }, 1000 * 5)
        })
    },
    CheckPresenterAPI () {
      let that = this
      this.presenterName = ''
      if (this.presenter === '') {
        this.$store.state.waitDialog = true
        this.toast = 'presenter is empty'
        setTimeout(function () {
          that.$store.state.waitDialog = false
        }, 1000 * 3)
        return
      }
      this.$http.get('/user/id/' + this.presenter, {timeout: 3000, emulateJSON: true})
        .then(function (response) {
          let res = response.data
          if (res.length > 0) {
            that.presenterName = 'presenter:' + res[0].nickname
          } else {
            that.presenterName = 'not find'
          }
        })
    },
    test () {
      this.$http.get('/user/name/e', {timeout: 3000, emulateJSON: true})
        .then(function (response) {
          // console.log('gatways', gateways[i], response, response.status)
          console.log('get', response)
        })
    },
    post () {
      let data = {nickname: 'name2', password: 'pass2'}
      this.$http.post('/user', data)
        .then(function (response) {
          // console.log('gatways', gateways[i], response, response.status)
          console.log('post', response)
        })
    },
    put () {
      let data = {nickname: 'putname2', password: 'passput'}
      this.$http.put('/user/1', data)
        .then(function (response) {
          // console.log('gatways', gateways[i], response, response.status)
          console.log('put', response)
        })
    }

  },
  beforeDestroy () {
    // 组件结束前执行
  }
}
</script>

<style scoped>

</style>



